import * as equipErrApi from '@/api/equip_err';
import {trErrTypeObj, feederErrObj, envErrTypeObj} from '@/common/const/sbyc';

export default {
  methods: {
    // 获取当日所有配变；
    getAllTrWarn() {
      return new Promise((resolve, reject) => {
        equipErrApi.getAllTrWarn().then((res) => {
          // console.log(res);
          // let data = res.data.resultValue.items;
          let data = res;
          this.setTrTableList(data);
          if(!data.length) {
            this.$toast('当日配变无异常')
          }
          resolve(res);
        }).catch(err => {
          console.log(err);
          reject(err);
        })
      })
    },
    // 获取当日配变异常 - 参数；
    getTodayTrWarn() {
      if(!this.trWarnReq.feederId) {
        this.$toast('还未选择线路');
        return;
      }
      return new Promise((resolve, reject) => {
        equipErrApi.getTodayTrWarn(this.trWarnReq).then(res => {
          // let data = res.data.resultValue.items;
          let data = res;
          this.setTrTableList(data);
          if(!data.length) {
            // this.$toast('当日配变无异常')
          }
          resolve(res);
        }).catch(err => {
          // console.log(err);
          reject(err);
        })
      })
    },
    // 获取变电站列表
    // getSubstationByAreaId(areaId) {
    //   let req = {};
    //   if(areaId) {
    //     req.areaId = areaId;
    //   } else {
    //     req = null;
    //   }
    //   return new Promise((resolve, reject) => {
    //     equipErrApi.getSubstationByAreaId(req).then(res => {
    //       console.log(res);
    //       resolve(res);
    //     }).catch(err => {
    //       console.log(err);
    //       reject(err);
    //     })
    //   })
    // },
    setTrTableList(data) {
      let tmp = [];
      data.forEach(item => {
        tmp.push({
          // 名称，异常类型，线路，
          a: item.trName,
          b: item.status,
          c: item.feederId,
          ...item
        })
      })
      this.setTableList(tmp);
    },
    setFeederTableList(data) {
      let tmp = [];
      data.forEach(item => {
        tmp.push({
          // 名称，异常类型，变电站，
          a: item.feeder_name,
          b: feederErrObj[item.abnormal_type],
          c: item.st_name,
          ...item
        })
      })
      this.setTableList(tmp);
    },
    setEnvTableList(data){
      if(data.length) {
        let tmp = [];
        data.forEach(item => {
          tmp.push({
            // 名称，异常类型，线路，
            a: item.name,
            b: envErrTypeObj[item.warn_type],
            c: item.feeder_name,
            ...item
          })
        })
        console.log(tmp);
        // debugger;
        this.setTableList(tmp);
      } else {
        this.setTableList([]);
        // this.$toast('无环境异常');
      }
    },
    getHisTrWarn() {
      if(!this.hisTrWarnReq.feederId) {
        this.$toast('还未选择线路');
        return;
      }
      return new Promise((resolve, reject) => {
        equipErrApi.getHisTrWarn(this.hisTrWarnReq).then(res => {
          // let data = res.data.resultValue.items;
          let data = res;
          this.setTrTableList(data);
          resolve(res);
        }).catch(err => {
          // console.log(err);
          reject(err);
        })
      })
    },
    getTodayEnvirAbnormal() {
      return new Promise((resolve, reject) => {
        equipErrApi.getTodayEnvirAbnormal(this.envErrReq).then(res => {
          // let data = res.data.resultValue.items;
          let data = res;
          this.setEnvTableList(data);
          resolve(res);
        }).catch(err => {
          console.log(err);
          reject();
        })
      })
    },
    getHisEnvWarn() {
      if(!this.hisEnvReq.feederId) {
        this.$toast('还未选择线路');
        return;
      }
      return new Promise((resolve, reject) => {
        equipErrApi.getHisEnvirAbnormal(this.hisEnvReq).then(res => {
          // console.log(res);
          // let data = res.data.resultValue.items;
          let data = res;
          this.setEnvTableList(data);
          resolve(res);
        }).catch(err => {
          // console.log(err);
          reject(err);
        })
      })
    },
    // 当日线路异常
    getTodayFeederAbnormal() {
      return new Promise((resolve, reject) => {
        console.log(this.feederErrReq, 'getTodayFeederAbnormal');
        equipErrApi.getTodayFeederAbnormal(this.feederErrReq).then(res => {
          // let data = res.data.resultValue.items;
          let data = res;
          this.setFeederTableList(data);
          if(!data.length) {
            // this.$toast('当日无线路异常');
          }
          resolve(res);
        }).catch(err => {
          // console.log(err);
          reject(err);
        })
      })
    },
  }
}


