import axios from 'axios';
import {baseURL} from '@/common/const/common';


axios.defaults.baseURL = baseURL;
axios.defaults.timeout = 5000;

const request = (para) => {
  return new Promise((resolve, reject) => {
    console.log(para);
    axios({
      method: 'post',
      url: para.url || '',
      data: para.data || '',
      responseType: para.dataType || 'json',
      headers: {
        'content-type': 'application/json;charset=UTF-8',
        // 'Access-Control-Allow-Origin': 'http://localhost:8080'
      },
    }).then(res => {
      console.log(res, para.url);
      let msg = '';
      resolve(res);
    }).catch(err => {
      console.log(err, para.url);
      reject(err)
    });
  })
};

export default request;


