const util = {
  deepClone(obj) {
    return JSON.parse(JSON.stringify(obj));
  },
  dashDateWithoutSpace(value) {
    if(!(value instanceof Date)) return;
    const formatObj = {
      y: value.getFullYear(),
      m: value.getMonth() + 1,
      d: value.getDate(),
    }
    formatObj.m < 10 ? formatObj.m = '0' + formatObj.m : '';
    formatObj.d < 10 ? formatObj.d = '0' + formatObj.d : ''; 
    return formatObj.y + '-' + formatObj.m + '-' + formatObj.d;
  },
  dashDateTimeWithoutSpace(value) {
    if(!(value instanceof Date)) return;
    const formatObj = {
      y: value.getFullYear(),
      m: value.getMonth() + 1,
      d: value.getDate(),
    }
    formatObj.m < 10 ? formatObj.m = '0' + formatObj.m : '';
    formatObj.d < 10 ? formatObj.d = '0' + formatObj.d : ''; 
    let date = formatObj.y + '-' + formatObj.m + '-' + formatObj.d;
    
    let h = value.getHours();
    let m = value.getMinutes();
    let s = value.getSeconds();
    h < 10 ? h = '0' + h: '';
    m < 10 ? m = '0' + m: '';
    s < 10 ? s = '0' + s: '';

    return `${date} ${h}:${m}:${s}`;
  },

  // 没用过的
  urlParse: url => {
    return (url.match(/([^?=&]+)(=([^&]*))/g) || []).reduce(
      (a, v) => ((a[v.slice(0, v.indexOf('='))] = v.slice(v.indexOf('=') + 1)), a), {}
    );
  },
  throttle(fn, delay, interval) {
    let timer = null;
    let prev = null;
    return function () {
      let _this = this;
      let args = arguments;
      let now = +new Date();

      if (!prev) prev = now;

      if (interval && (now - prev > interval)) {
        fn.apply(_this, args);
        prev = now;
        clearTimeout(timer);
      } else {
        clearTimeout(timer);
        timer = setTimeout(function () {
          fn.apply(_this, args);
          prev = null;
        }, delay)
      }
    }
  },
  debounce(func, wait, immediate) {
    let timeout, args, context, timestamp, result
  
    const later = function() {
      // 据上一次触发时间间隔
      const last = +new Date() - timestamp
  
      // 上次被包装函数被调用时间间隔last小于设定时间间隔wait
      if (last < wait && last > 0) {
        timeout = setTimeout(later, wait - last)
      } else {
        timeout = null
        // 如果设定为immediate===true，因为开始边界已经调用过了此处无需调用
        if (!immediate) {
          result = func.apply(context, args)
          if (!timeout) context = args = null
        }
      }
    }
  
    return function(...args) {
      context = this
      timestamp = +new Date()
      const callNow = immediate && !timeout
      // 如果延时不存在，重新设定延时
      if (!timeout) timeout = setTimeout(later, wait)
      if (callNow) {
        result = func.apply(context, args)
        context = args = null
      }
  
      return result
    }
  },
  // 寻找ele元素 含有指定class的祖先元素
  parentUntil(ele, className) {
    // 没有父元素
    if(!ele.parentNode.tagName) return;
    // 如果是自己本身的话
    if(ele.classList.contains(className)) {
      return ele;
    }
    if(ele.parentNode.classList.contains(className)) {
      return ele.parentNode;
    } else {
      return this.parentUntil(ele.parentNode, className);
    }
  },
  isPhoneNumber(value){
    let reg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
    return reg.test(Number(value));
  },
  arrGetItemSpecKey(arr = [] , key) {
    let temp = [];
    arr.forEach(item => {
      temp.push(item[key])
    })
    return temp;
  },
  // 返回类型
  getType(x){
    return Object.prototype.toString.call(x).match(/\[object\s(\w+)/)[1].toLowerCase() || '';
  },
}

export default util;


