<template>
  <div id="six-detail">
    <van-nav-bar @click-left="goBack" title="终端台账" class="nav-bar">
      <div class="goback-btn-wp" slot="left">
        <img src="./zong-fanhui.png" alt="" class="goback-btn">
      </div>
    </van-nav-bar>
    <split-view></split-view>
    <div class="main-wp">
      <div class="table-wp">
        <div class="table-main">
          <div v-for="(item, index) in termInfo" :key="index" class="table-item border-1px">
            <div class="key-wp">
              <span class="item-key">{{item.label}}</span>
            </div>
            <div class="value-wp">
              <span class="item-value">{{item.value}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// util
import _util from '@/util/index';
// 组件
import SplitView from '@/components/split-view/index';
// 终端台账API
import * as zdtzApi from '@/api/zdtaizhang';
// const
import {channelConditionTypeObj} from '@/common/const/dztz';

function dataLabelList() {
  return [
    {
      label: '终端名称',
      value: '',
    },
    {
      label: '馈线名称',
      value: '',
    },
    {
      label: '终端类型',
      value: '',
    },
    {
      label: '终端状态',
      value: '',
    },
    {
      label: '通道工况',
      value: '',
    },
    {
      label: '对时时间差值',
      value: '',
    },
    {
      label: '投运时间',
      value: '',
    },
    {
      label: '所属厂家',
      value: '',
    },
  ]
}

export default {
  data() {
    return {
      termId: '',
      termInfo: dataLabelList(),
    }
  },
  methods: {
    goBack(){
      this.$router.replace({path: '/six'});
    },
    getTermInfo() {
      if(this.termId) {
        zdtzApi.getTermInfoApi({
          termId: this.termId
        }).then(res => {
          // let data = res.data[0];
          let data = res[0];
          console.log(data);
          if(data.length) {
            data.forEach((item, index) => {
              switch(index) {
                case 0:
                  this.termInfo[0].value = item;
                  break;
                case 1:
                  this.termInfo[1].value = item;
                  break;
                case 2:
                  this.termInfo[2].value = item;
                  break;
                case 3: 
                  this.termInfo[3].value = item;
                  break;
                case 4:
                  this.termInfo[5].value = item;
                  break;
                case 6:
                  if(index === 6 && (typeof item === 'number')) {
                    item = _util.dashDateWithoutSpace(new Date(item));
                  }
                  this.termInfo[6].value = item;
                  break;
                case 7:
                  this.termInfo[7].value = item;
                  break;
                case 9:
                  this.termInfo[4].value = channelConditionTypeObj[item];
                  break;
                default:
                  break;
              }
            })
          } else {
            this.$toast('获取不到终端台账信息');
          }
        }).catch(err => {
          throw new Error(err);
          this.$toast('获取终端台账信息失败');
        })
      }
      else {
        this.$toast('没有终端ID');
      }
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.termId = to.params.item.id || '';
    })
  },
  created() {
    this.$nextTick(() => {
      this.getTermInfo();
    })
  },
  components: {
    SplitView
  },
  deactivated() {
    this.$destroy();
    this.termInfo = dataLabelList();
  }
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#six-detail{
  @extend %nav-with-one-title;
  .main-wp{
    .table-wp{
      background-color: #f3f3f3;
      .table-item{
        padding: 15px 0;
        background-color: #fff;
        @include border-1px(#dfdfdf);
        font-size: 0;
        .key-wp,
        .value-wp{
          display: inline-block;
          vertical-align: middle;
          line-height: 1.5;
          font-size: 14px;
          box-sizing: border-box;
        }
        .key-wp{
          padding: 0 15px 0 20px;
          width: 35%;
          color: #666;
        }
        .value-wp{
          padding-right: 15px;
          width: 60%;
          font-size: 14px;
        }
        .item-value{
          font-size: 14px;
          word-break: break-all;
          color: #999;
          &::after{
            content: none;
          }
        }
      }
    }
  }
}
</style>



