import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/views/index/index';
import One from '@/views/one/index';
import Two from '@/views/two/index';
import Three from '@/views/three/index';
import Four from '@/views/four/index';
import Five from '@/views/five/index';
import Six from '@/views/six/index';
import Seven from '@/views/seven/index';


import ThreeDetail from '@/views/three/components/three-detail';
import FiveDetail from '@/views/five/components/five-detail';
import SixDetail from '@/views/six/components/six-detail';

import AddSubscribe from '@/views/seven/components/add-subs';
import equipmentSelect from '@/views/seven/components/equipment-select/index';

import addDefect from '@/views/four/components/add-defect/index';

import confirmDefect from '@/views/four/components/confirm-defect/index';
import confirmedDefect from '@/views/four/components/confirmed-defect/index';

import oneMap from '@/views/one/components/map/index';
import lineMap from '@/views/one/components/line-map/index';

import FourDeviceSel from '@/components/device-single-sel/index';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      alias: '/index',
      component: Index
    },
    {
      path: '/one',
      name: 'one',
      component: One
    },
    {
      path: '/two',
      name: 'two',
      component: Two
    },
    {
      path: '/three',
      name: 'three',
      component: Three,
    },
    {
      path: '/four',
      name: 'four',
      component: Four,
    },
    {
      path: '/five',
      name: 'five',
      component: Five,
    },
    {
      path: '/six',
      name: 'six',
      component: Six,
    },
    {
      path: '/seven',
      name: 'seven',
      component: Seven,
    },
    {
      path: '/three/detail',
      name: 'ThreeDetail',
      component: ThreeDetail,
    },
    {
      path: '/five/detail',
      name: 'FiveDetail',
      component: FiveDetail,
    },
    {
      path: '/six/detail',
      name: 'SixDetail',
      component: SixDetail,
    },
    {
      path: '/seven/addsubs',
      name: 'AddSubscribe',
      component: AddSubscribe,
    },
    {
      path: '/seven/addsubs/equipmentselect',
      name: 'equipmentSelect',
      component: equipmentSelect,
    },
    {
      path: '/four/adddefect',
      name: 'addDefect',
      component: addDefect,
    },
    {
      path: '/four/confirmdefect',
      name: 'confirmDefect',
      component: confirmDefect,
    },
    {
      path: '/four/confirmeddefect',
      name: 'confirmedDefect',
      component: confirmedDefect,
    },
    {
      path: '/one/map',
      name: 'oneMap',
      component: oneMap,
    },
    {
      path: '/one/linemap',
      name: 'lineMap',
      component: lineMap,
    },
    {
      path: '/four/devicesel',
      name: 'FourDeviceSel',
      component: FourDeviceSel,
    }
  ]
})



