// import request from '@/util/request';
import jsonp from '@/util/jsonp.js';

function joinObjAsQuery(obj) {
  return Object.values(obj).join('/');
}

// 加载终端类型数据
// export function getTermTypesApi() {
//   return request({
//     url: 'terminalController/rest/termTz/termType',
//     method: 'get',
//   })
// }
export function getTermTypesApi() {
  let url = '/terminalController/rest/termTz/termType';
  return jsonp(url);
}

// 加载表格数据
