import * as smsApi from '@/api/smsSuscribe';
import _util from '@/util/index';
import {errTypeObj} from '@/common/const/const';

export default {
  methods: {
    getDevList() {
      let req = {
        userId: this.userId,
        devType: this.devType,
      };
      // this.tableList = [];
      return new Promise((resolve, reject) => {
        smsApi.getDevList(req).then(res => {
          console.log(res, 'getDevList');
          // let data = res.data;
          let data = res;
          if(data.length) {
            this.tableList = this.changeDevListStruct(data);
          } else {
            // this.$toast('没有任何订阅');
          }
          resolve(res);
        }).catch(err => {
          reject(err);
        })
      })
    },
    changeDevListStruct(data) {
      // console.log(data);
      data.forEach(item => {
        let warnLen = 0;
        item.warnSubMod = [];
        item.warnSub.forEach(warnItem => {
          warnLen += warnItem.warnList.length;
          warnItem.warnList.forEach(warnStatus => {
            let warnSubItem = [];
            warnSubItem.warnType = warnItem.warnType;
            warnSubItem.warnTypeLabel = errTypeObj[warnItem.warnType].label;
            warnSubItem.warnStatus = warnStatus;
            warnSubItem.warnStatusLabel = errTypeObj[warnItem.warnType].sub[warnStatus].label;
            item.warnSubMod.push(warnSubItem)
          })

        })
        item.warnLen = warnLen;
      })
      console.log(data, 'changeDevListStruct');
      return data;
    },
    getWarnList() {
      console.log
      let req = {
        userId: this.userId,
        devType: this.devType,
      }
      // console.log(req);
      return new Promise((resolve, reject) => {
        smsApi.getWarnList(req).then(res => {
          console.log(res, 'getWarnList');
          // let data = res.data;
          let data = res;
          if(data.length) {
            this.tableList = this.changeWarnListStruct(data);
            // console.log(this.tableList,'sss');
          } else {
            // this.$toast('没有任何订阅');
          }
          resolve();
        }).catch(err => {
          reject(err);
        })
      })
    },
    changeWarnListStruct(data) {
      data.forEach(item => {
        item.warnTypeLabel = errTypeObj[item.warnType].label;
        item.warnStatusLabel = errTypeObj[item.warnType].sub[item.warnStatus].label;
      })
      console.log(data, 'changeWarnListStruct');
      return data;
    },
    deleteSubscribe(data) {
      return new Promise((resolve, reject) => {
        smsApi.deleteSubscribe(data).then(res => {
          console.log(res);
          // let data = res.data.resultValue.items[0];
          let data = res[0];
          if(data && data.message) {
            this.$toast(data.message);
            if(this.subbarActive === 0) {
              this.getDevList();
            } else {
              this.getWarnList();
            }
          }
          resolve();
        }).catch(err => {
          reject(err);
        })
      })
    },
    addSubscribe(data) {
      return new Promise((resolve, reject) => {
        smsApi.addSubscribe(data).then(res => {
          // let data = res.data.resultValue.items[0];
          let data = res[0];
          if(data.message !== '保存成功') {
            throw new Error('保存失败')
          } else {
            resolve();
          }
        }).catch(err => {
          reject(err);
        })
      })
    },
  }
}


