<template>
  <div class="line-mp-wp">
    <div class="nav-wp">
      <van-nav-bar title="单线图" @click-left="goBack" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="../../zong-fanhui.png" alt="" class="goback-btn">
        </div>
      </van-nav-bar>
    </div>
    <iframe class="main-wp" src="" ref="mainWp" name="line-map"></iframe>
  </div>
</template>

<script>
import _util from "@/util/index";
import {lineMapBaseURL} from '@/common/const/common';

export default {
  data() {
    return {
      dxt: '',
      lineMapBaseURL,
    }
  },
  methods: {
    goBack() {
      this.$router.push("/one/map");
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.dxt= to.params.dxt;
    })
  },
  created() {
    this.$nextTick(() => {
      this.$refs['mainWp'].src = this.lineMapBaseURL + this.dxt;
      // this.$refs['mainWp'].innerHTML = this.lineMapBaseURL + this.dxt;
    })
  },
  // 不需要保留页面状态
  deactivated() {
    this.$destroy();
  }
}
</script>


<style lang="scss" scoped>
@import "@/common/scss/index.scss";

.line-mp-wp {
  height: 100%;
  @extend %nav-with-one-title;
  .nav-wp{
    height: 48px;
  }
  .main-wp {
    background: red;
    position: absolute;
    left: 0;
    top: 48px;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
  }
}
</style >


