<template>
  <div class="indicator-wp">
    <span class="indicator" :class="{active: active1}"></span>
    <span class="indicator" :class="{active: active2}"></span>
    <span class="indicator" :class="{active: active3}"></span>
    <span class="indicator" :class="{active: active4}"></span>
  </div>
</template>

<script>
/**
 * active1 开关跳闸；
 * active2 离线FA；
 * active3 接地动作
 * active4 配变失电
*/
export default {
  data() {
    return {
      active1: false,
      active2: false,
      active3: false,
      active4: false,
    }
  },
  props: {
    gzItem: {
      type: Object,
    }
  },
  watch: {
    gzItem: {
      // 与map里面故障类型判断关联
      handler(item) {
        if (item.fault_type === 0) {
          // 开关跳闸
          this.active1 = true;
        } 
        else if (item.fault_type === 1) {
          // 离线FA
          this.active2 = true;
        } 
        else if (item.fault_type === 2 || item.fault_type === 4) {
          // 接地动作
          this.active3 = true;
        } 
        else if (item.fault_type === 3) {
          // 配变失电
          this.active4 = true;
        } 
        else if (item.fault_type >= 10) {
          if (item.fault_type % 10 === 0) {
            // 开关跳闸 + 配变失电
            this.active1 = this.active4 = true;
          } 
          else if (item.fault_type % 10 === 1) {
            // 离线FA + 配变失电
            this.active2 = this.active4 = true;
          } 
          else if (item.fault_type % 10 === 2 || item.fault_type % 10 === 4) {
            // 配变失电 + 接地动作
            this.active3 = this.active4 = true;
          }
        }
      },
      immediate: true
    }
  }
}
</script>

<style lang="scss" scoped>
.indicator-wp{
position: absolute;
right: 15px;
top: 21px;
font-size: 0;
.indicator{
  display: inline-block;
  margin-left: 4px;
  height: 7px;
  width: 7px;
  background: #eee;
  border-radius: 7px;
  &:nth-child(1).active {
    background: #e95c07;
  }
  &:nth-child(2).active {
    background: #008ad3;
  }
  &:nth-child(3).active {
    background: #b654d2;
  }
  &:nth-child(4).active {
    background: #2bbc90;
  }
}
}
</style>


