<template>
  <div id="two">
    <van-nav-bar
      title="停运信息"
      left-arrow
      @click-left="goBack"
      class="nav-bar"
    />
  </div>  
</template>

<script>
export default {
  methods: {
    onClickLeft() {
      console.log('返回上一页');
    },
    goBack(){
      // this.$router.replace({path: '/'});
      // android go to index.html;
      webCtrl.finish()
    }
  }
}
</script>

<style lang="scss">
#two{
  .nav-bar{
    font-size: 17px;
    color: #fff;
    background-color: #1bbc9b;
    .goback-btn-wp{
      line-height: 48px;
      .goback-btn{
        width: 9.5px;
        height: 17px;
      }
    }
    .nav-title{
      line-height: 48px;
      display: flex;
      justify-content: center;
      align-items: center;
      .dropdown-btn{
        margin-left: 4px;
        width: 9px;
        height: 5px;
      }
    }
  }
  .van-nav-bar{
    height: 48px;
  }
}
</style>


