<template>
  <div id="three-detail">
    <van-nav-bar @click-left="goBack" :title="navTitleList[navActiveIndex]" class="nav-bar">
      <div class="goback-btn-wp" slot="left">
        <img src="../zong-fanhui.png" alt="" class="goback-btn">
      </div>
    </van-nav-bar>
    <div class="main-wp" v-if="navActiveIndex === 0">
      <div class="equiment-wp">
        <div class="name">
          <span class="diamond"></span>
          <span class="title">设备名称：</span>
          <span class="value">{{item.trName}}</span>
        </div>
        <div class="name">
          <span class="diamond warning"></span>
          <span class="title">异常类型：</span>
          <span class="value">{{trErrTypeObj[item.status]}}</span>
        </div>
      </div>
      <div class="table-wp">
        <div class="title-wp clearfix">
          <span class="mesure-name">量测名称</span>
          <span class="mesure-value">当前值</span>
        </div>
        <div class="table-main">
          <div class="table-item border-1px">
            <span class="item-key">负载率：</span>
            <span class="item-value">{{item.loadrate}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">三相电压：</span>
            <span class="item-value">{{item.u}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">三项电流：</span>
            <span class="item-value">{{item.i}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">有功：</span>
            <span class="item-value">{{item.p}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">无功：</span>
            <span class="item-value">{{item.q}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">额定容量：</span>
            <span class="item-value">{{item.rateP}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">壳温：</span>
            <span class="item-value">{{item.temp}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="main-wp" v-if="navActiveIndex === 1">
      <div class="equiment-wp">
        <div class="name">
          <span class="diamond"></span>
          <span class="title">线路名称：</span>
          <span class="value">{{item.feeder_name}}</span>
        </div>
        <div class="name">
          <span class="diamond warning"></span>
          <span class="title">异常类型：</span>
          <span class="value">{{feederErrObj[item.abnormal_type]}}</span>
        </div>
      </div>
      <div class="table-wp">
        <div class="title-wp clearfix">
          <span class="mesure-name">量测名称</span>
          <span class="mesure-value">当前值</span>
        </div>
        <div class="table-main">
          <div class="table-item border-1px">
            <span class="item-key">A相电流：</span>
            <span class="item-value">{{item.i_a_value}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">B相电流：</span>
            <span class="item-value">{{item.i_b_value}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">C相电流：</span>
            <span class="item-value">{{item.i_c_value}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">有功：</span>
            <span class="item-value">{{item.p}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">无功：</span>
            <span class="item-value">{{item.q}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="main-wp" v-if="navActiveIndex === 2">
      <div class="equiment-wp">
        <div class="equiment-wp">
          <div class="name">
            <span class="diamond"></span>
            <span class="title">站所名称：</span>
            <span class="value">{{item.name}}</span>
          </div>
          <div class="name">
            <span class="diamond warning"></span>
            <span class="title">异常类型：</span>
            <span class="value">{{item.b}}</span>
          </div>
        </div>
      </div>
      <div class="table-wp">
        <div class="title-wp clearfix">
          <span class="mesure-name">量测名称</span>
          <span class="mesure-value">当前值</span>
        </div>
        <div class="table-main">
          <div class="table-item border-1px">
            <span class="item-key">A相电流：</span>
            <span class="item-value">{{item.i_a_value}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">B相电流：</span>
            <span class="item-value">{{item.i_b_value}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">C相电流：</span>
            <span class="item-value">{{item.i_c_value}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">有功：</span>
            <span class="item-value">{{item.p}}</span>
          </div>
          <div class="table-item border-1px">
            <span class="item-key">无功：</span>
            <span class="item-value">{{item.q}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// vuex
import {mapGetters} from 'vuex';
// api
import * as equipErrApi from '@/api/equip_err';
// const
import {trErrTypeObj, feederErrObj, envErrTypeObj} from '@/common/const/sbyc';

const navTitleList = ['配变异常详情', '线路异常详情', '环境异常详情'];
// 配变err
const TRErrTableKeyList = {
  loadrate: '负载率',
  u: '三相电压', 
  i: '三项电流',
  p: '有功',
  q: '无功',
  rateP: '额定容量',
  temp: '壳温',
};
// 线路err
const feederErrTableKeyList = {
  i_a_value: 'A相电流',
  i_b_value: 'B相电流',
  i_c_value: 'C相电流',
  p: '有功',
  q: '无功',
}
const feederErrList = {
  '1': '过载',
  '2': '重载'
}

export default {
  data() {
    return {
      navTitleList,
      navActiveIndex: null,
      index: '',
      feederId: '',
      item: '',

      loading: false,
      finished: true,

      trErrTypeObj,
      feederErrObj,
      envErrTypeObj,
    }
  },
  computed: {
    ...mapGetters(['tableList']),
  },
  watch: {
    index: {
      handler() {
        this.item = this.tableList[this.index];
      },
      immediate: true,
    }
  },
  methods: {
    goBack(){
      this.$router.go(-1);
    },
    onLoad() {

    }
  },
  beforeRouteEnter (to, from, next) {
    console.log(to.params);
    next(vm => {
      vm.navActiveIndex = to.params.navActiveIndex;
      vm.feederId = to.params.feederId;
      vm.index = to.params.index;
      if(to.params.navActiveIndex === 1 || to.params.navActiveIndex === 2) {
        equipErrApi.getFeederMeasure({
          feederId: to.params.feederId
        }).then((res) => {
          // let data = res.data.resultValue.items[0];
          let data = res[0];
          vm.item = Object.assign({}, vm.item, data);
          console.log(vm.item, '1111');
        }).catch(err => {
          console.log(err);
        })
      }
    })
  },
  deactivated() {
    this.$destroy();
  }
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#three-detail{
  @extend %nav-with-one-title;
  .main-wp{
    .name{
      display: flex;
      align-items: center;
      padding: 5px 0 5px 10px;
      text-align: left;
      height: 45px;
      line-height: 45px;
      font-size: 15px;
      .diamond{
        margin-top: -3px;
        margin-right: 10px;
        width: 9px;
        height: 9px;
        background: #1bbc9b;
        &.warning {
          background: #e9a945;
        }
      }
    }
    .table-wp{
      background-color: #f3f3f3;
      .title-wp{
        height: 40px;
        line-height: 40px;
        background-color: #33d8b6;
        color: #fff;
        font-size: 0;
        .mesure-name,
        .mesure-value{
          display: inline-block;
          padding-left: 8%;
          width: 50%;
          font-size: 16px;
        }
      }
      .table-item{
        height: 50px;
        line-height: 50px;
        font-size: 0;
        background-color: #fff;
        @include border-1px(#dcdcdc);
        .item-key,
        .item-value{
          display: inline-block;
          padding-left: 8%;
          width: 50%;
          font-size: 14px;
        }
      }
    }
  }
  .van-nav-bar{
    line-height: 48px;
  }
  .van-cell__value--alone {
    padding-left: 32px;
    text-align: left;
    display: flex!important;
    justify-content: space-around!important;
  }
}
</style>



