    },
    showNavPopup() {
      this.navPopupShow = true;
    },
    changeNav(item, index) {
      this.navActiveIndex = index;
      this.navPopupShow = false;
    },
    getTermInfoApi() {
      let req = {
        termId: this.id,
      }
      return new Promise((resolve, reject) => {
        dzdyApi.getTermInfoApi(req).then(res => {
          // let data = res.data;
          console.log(res, 'getTermInfoApi');
          // debugger;
          let data = res;
          this.IntePara.forEach(item1 => {
            data.forEach(item2 => {
              if(item1.label === item2[1]) {
                item1.value = item2[2];
              }
            })
          })
          this.operPara.forEach(item1 => {
            data.forEach(item2 => {
              if(item1.label === item2[1]) {
                item1.value = item2[2];
              }
            })
          })
          this.constPara.forEach(item1 => {
            data.forEach(item2 => {
              if(item1.label === item2[1]) {
                item1.value = item2[2];
              }
            })
          })
          console.log(this.IntePara);
          resolve();
        }).catch(err => {
          reject(err);
        })
      })
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.id = to.params.id || '';
    })
  },
  created() {
    this.$nextTick(() => {
      this.getTermInfoApi();
    })
