<style lang="scss">
@import "@/common/scss/index.scss";

#five-detail{
  height: 100%;
  .nav-wp{
    height: 90px;
  }
  @extend %nav-with-one-title;
  .nav-popup {
    top: 92px;
    display: flex;
    justify-content: space-around;
    padding: 26px 9px;
    width: 100%;
    background-color: #f6f6f6;
    .item{
      padding: 0 9.5px;
      line-height: 35px;
      font-size: 14px;
      border: 1px solid #dcdcdc;
      border-radius: 3px;
      &.active{
        color: #1bbc9b;
      }
    }
  }
  .van-popup{
    width: 100%;
  }
  .main-wp{
    height: calc(100% - 63px);
    overflow-y: auto;
    .table-wp{
      background-color: #f3f3f3;
      .title-wp{
        height: 40px;
        line-height: 40px;
        font-size: 0;
        background-color: #33d8b6;
        color: #fff;
        .mesure-name,
        .mesure-value{
          display: inline-block;
          padding-left: 15px;
          width: 50%;
          font-size: 14px;
        }
      }
      .table-item{
        padding: 18px 0;
        background-color: #fff;
        font-size: 0;
        @include border-1px(#dfdfdf);
        line-height: 1.5;
        .key-wp,
        .value-wp{
          display: inline-block;
          padding-left: 15px;
          width: 50%;
          font-size: 14px;
          color: #666;
          vertical-align: middle;
        }
        .item-value{
          width: 50%;
          font-size: 14px;
          color: #c7c7c7;
          word-break: break-word;
        }
      }
    }
  }
  .van-cell{
    padding: 0;
    line-height: 50px;
    .van-field__control {
      font-size: 14px;
      color: #c7c7c7;
      &:focus {
        color: #666;
      }
    }
    input:-ms-input-placeholder{
      color: #c7c7c7;
    }
    input::-webkit-input-placeholder{
      color: #c7c7c7;
    }
  }
}
</style>



