<template>
  <div id="equip-sel">
    <div class="nav-wp">
      <van-nav-bar title="选择终端设备" @click-left="goBack" @click-right="equipSelConfirm" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="../../zong-fanhui.png" alt="" class="goback-btn">
        </div>
        <div class="confirm-btn" slot="right">确定</div>
      </van-nav-bar>
      <!-- <van-tabbar v-model="subTabbarActive" class="sub-tabbar">
        <van-tabbar-item>
          <span>区域</span>
          <div slot="icon" slot-scope="props" class="sub-tabbar-icon-wp">
            <img src="./xiala02.png" alt="" v-if="props.active">
            <img src="./xiala01.png" alt="" v-else>
          </div>
        </van-tabbar-item>
        <van-tabbar-item>
          <span>线路</span>
          <div slot="icon" slot-scope="props" class="sub-tabbar-icon-wp">
            <img src="./xiala02.png" alt="" v-if="props.active">
            <img src="./xiala01.png" alt="" v-else>
          </div>
        </van-tabbar-item>
      </van-tabbar> -->
      <!-- sub navbar -- search -->
      <van-tabbar v-model="subbarActive" class="sub-tabbar">
        <van-tabbar-item v-for="(item, index) in subbarList" :key="index" @click="subbarItemClick(item, index)">
          <span>{{item.activeItemValue || item.label}}</span>
          <div slot="icon" slot-scope="props" class="sub-tabbar-icon-wp">
            <img src="./xiala02.png" alt="" v-if="props.active">
            <img src="./xiala01.png" alt="" v-else>
          </div>
        </van-tabbar-item>
      </van-tabbar>
    </div>
    <cube-index-list :data="equipmentData" @select="selectItem">
      <cube-index-list-group v-for="(group, index) in equipmentData" :key="index" :group="group">
        <cube-index-list-item v-for="(item, index) in group.items" :key="index" :item="item" @select="selectItem">
          <div class="item">
            <span>{{item.name}}</span>
            <div class="img-wp">
              <img src="./shebei-xuanzhong.png" alt="" v-if="item.selected" class="sel-img">
              <img src="./shebei-weixuan.png" alt="" v-else class="sel-img">
            </div>
          </div>
        </cube-index-list-item>
      </cube-index-list-group>
    </cube-index-list>
    <!-- 条件筛选 popup -->
    <van-popup v-model="subbarPopupShow" class="filter-popup" overlay-class="filter-overlay">
      <span class="item" v-for="(item, index) in filterList" :key="index" @click="subbarPopupItemClick(item, index)" :class="{active: subbarPopupItemActive === index}">{{item.label}}</span>
    </van-popup>
  </div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex';
import commonApiMixin from '@/mixins/common-api-mixin';
import {deviceTypeList} from '@/common/const/const';

const subbarList1 = [
  {label: '区域', type: 0, activeItemIndex: 0, activeItemValue: ''},
  {label: '线路', type: 1, activeItemIndex: 0, activeItemValue: ''},
];
const subbarList2 = [
  {label: '区域', type: 0, activeItemIndex: 0, activeItemValue: ''},
  {label: '变电站', type: 1, activeItemIndex: 0, activeItemValue: ''},
];

export default {
  mixins: [commonApiMixin],
  data() {
    return {
      subbarActive: -1,
      subbarList: [],
      subbarPopupShow: false,

      filterList: [],

      areaList: [],
      feederList: [],
      stationList: [],

      equipmentData: [],
      // subTabbarActive: -1,

      pushTimeType: '',
      errType: '',

      feederShow: false
    }
  },
  computed: {
    ...mapGetters(['equipSelList', 'deviceType']),
    // 显示 区域线路 或 区域变电站
    isFeedFilter() {
      return this.deviceType.id !== deviceTypeList[0].id;
    }
  },
  methods: {
    ...mapMutations({
      setEquipSelList: 'SET_EQUIP_SEL_LIST',
    }),
    goBack(){
      this.$router.replace({name: 'AddSubscribe'});
    },
    selectItem(item) {
      item.selected = !item.selected;
    },
    equipSelConfirm() {
      let selectedList = [];
      this.equipmentData.forEach((group) => {
        if(Array.isArray(group.items) && group.items.length) {
          group.items.forEach((item) => {
            if(item.selected) {
              selectedList.push(item);
            }
          })
        }
      })
      this.setEquipSelList(selectedList);
      this.$router.push({name: 'AddSubscribe'});
    },
    subbarItemClick(item, index) {
      // 点击区域
      if(item.type === 0) {
        this.filterList = this.areaList;
      }
      // 点击线路
      else if(item.type === 1 && this.isFeedFilter) {
        this.filterList = this.feederList;
      }
      // 点击变电站
      else if(item.type === 1 && !this.isFeedFilter) {
        this.filterList = this.stationList;
      }

      this.subbarPopupItemActive = item.activeItemIndex;
      this.subbarPopupShow = true;
    },
    subbarPopupItemClick(item, index) {
      if(this.subbarActive === 0) {
        this.subbarList[0].activeItemIndex = index;
        this.subbarList[0].activeItemValue = item.label;
        
        this.subbarList[1].activeItemIndex = -1;
        this.subbarList[1].activeItemValue = '';
        
        if(this.isFeedFilter) {
          this.getFeederByAreaId(item.id).then(() => {
            this.feederList.shift();
            this.subbarActive = 1;
            this.subbarItemClick(this.subbarList[1], 1);
          });
        }
        else {
          this.getSubstationByAreaId(item.id).then(() => {
            this.stationList.shift();
            this.subbarActive = 1;
            this.subbarItemClick(this.subbarList[1], 1);
          });
        }
      } 
      else if(this.subbarActive === 1) {
        this.subbarList[1].activeItemIndex = index;
        this.subbarList[1].activeItemValue = item.label;

        if(this.isFeedFilter) {
          this.getDeviceListByFeederId(item.id);
        }
        else {
          this.getDeviceListByStId(item.id);
        }
      }
      this.subbarPopupShow = false;
    },
    equipmentDataAddSelected() {
      this.equipmentData.forEach((group) => {
        if(Array.isArray(group.items) && group.items.length) {
          group.items.forEach((item) => {
            this.$set(item, 'selected', false);
            this.equipSelList.forEach(item2 => {
              if(item2.value === item.value) {
                this.$set(item, 'selected', true);
              }
            })
          })
        }
      })
    }
  },
  created() {
    if(this.isFeedFilter) {
      this.subbarList = subbarList1;
    }
    else {
      this.subbarList = subbarList2;
    }

    this.getAreaList().then(() => {
      this.areaList.shift();
      if(!this.areaList.length) return;
      this.subbarList[0].activeItemValue = this.areaList[0].label
      if(this.isFeedFilter) {
        this.getFeederByAreaId(this.areaList[0].id).then(() => {
          this.feederList.shift();
          if(!this.feederList.length) return;
          this.subbarList[1].activeItemValue = this.feederList[0].label
          let feederId = this.feederList[0].id;
          this.getDeviceListByFeederId(this.feederList[0].id);
        })
      } 
      else {
        this.getSubstationByAreaId().then(() => {
          this.stationList.shift();
          if(!this.stationList.length) return;
          this.subbarList[1].activeItemValue = this.stationList[0].label;
          let stationId = this.stationList[0].id;
          // console.log(this.stationList);
          this.getDeviceListByStId(stationId);
        });
        
      }
    });
  },
  deactivated() {
    this.$destroy();
  }
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";
#equip-sel{
  height: 100%;
  .nav-wp{
    height: 90px;
  }
  @extend %nav-with-one-title;
  @extend %sub-tabbar;
  @include subbar-popup(90px);
  .cube-index-list{
    height: calc(100% - 90px);
    .item{
      display: flex;
      justify-content: space-between;
      padding-right: 10px;
      .img-wp{
        display: flex;
        align-items: center;
        .sel-img{
          width: 18px;
          height: 18px;
        }
      }
    }
    .cube-index-list-anchor{
      margin-top: -1px;
      padding: 0 0 0 15px;
      height: 28px;
      line-height: 28px;
      font-size: 15px;
      color: #333;
      font-weight: bold;
      text-align: left;
    }
    .cube-index-list-item{
      padding:0 15px;
      height: 50px;
      line-height: 50px;
      font-size: 15px;
      text-align: left;
      color: #666;
      @include border-1px(#dfdfdf);
      &::after{
        width: auto;
        right: 20px;
      }
      
      .cube-index-list-item-def{
        padding: 0;
      }
    }
    .cube-index-list-item:last-child{
      &::after{
        content: none;
      }
    }
    .cube-index-list-nav{
      .cube-index-list-nav-item{
        padding: 0 8px 6px 8px;
        font-size: 12px;
        color: $theme-color;
      }
    }
  }
  .confirm-btn{
    padding: 0 10px;
  }
}
</style>



