import {baseURL} from '@/common/const/common';

export default function requestJquery({url, data, opts}) {
  url = baseURL + '/' + url;

  // console.log(url, data);
  return new Promise((resolve, reject) => {
    $.ajax({
      url,
      type: "post",
      data: data,
      contentType: 'application/x-www-form-urlencoded; charset=UTF-8',
      success(data) {
        console.log(data, 'upload success');
        resolve(data);
      },
      error(err) {
        console.log(err);
        reject(err);
      }
    })
  })
}


