<template>
  <div id="index">
    <router-link tag="div" :to="{path: '/one'}" class="green">故障研判</router-link>
    <router-link tag="div" :to="{path: '/two'}" class="delete">停运信息</router-link>
    <router-link tag="div" :to="{path: '/three'}" class="green">设备异常</router-link>
    <router-link tag="div" :to="{path: '/four'}" class="pink">缺陷管理</router-link>
    <router-link tag="div" :to="{path: '/five'}" class="green">定值调阅</router-link>
    <router-link tag="div" :to="{path: '/six'}" class="green">终端台账</router-link>
    <router-link tag="div" :to="{path: '/seven'}" class="green">消息订阅</router-link>
    <!-- <div id="test">
      <form id="upload-form" action="/upload" enctype="multipart/form-data" method="post">
        <input id="selece-files" type="file" name="fileToUpload" />
        <input id="do-upload" type="submit" value="上传" />
      </form>
    </div> -->
  </div>  
</template>

<script>
export default {
  methods: {
    submit() {
      var Upload = (function(win, upload) {
        upload.submit = function() {
          var self = this,
            form = document.getElementById("form"),
            formdata = new FormData(form),
            xhr = new XMLHttpRequest();

          // xhr.upload 在 iOS Safari、 大部分 Android 4.0+ 的自带浏览器、Chrome 都支持
          xhr.upload.addEventListener("progress", self.onProgress, false);
          xhr.addEventListener("load", self.onSuccess, false);
          xhr.addEventListener("error", self.onError, false);
          xhr.addEventListener("abort", self.onCancel, false);

          xhr.open("post", form.action, false);
          xhr.send(formdata);
        };

        // 可以在 onProgress 的时候处理进度条
        upload.onProgress = function(e) {
          if (e.lengthComputable) {
            var progress = Math.round(e.loaded * 100 / e.total) + "%";
            console.log("on progress: ", progress);
          }
        };

        upload.onError = function() {};
        upload.onCancel = function() {};

        // 上传完成
        upload.onSuccess = function() {};

        return upload;
      })(window, window.Upload || {});
    }
  }
};
</script>

<style lang="scss">
#index {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  background: #fff;
  div {
    line-height: 60px;
    font-size: 30px;
  }
  .pink {
    color: pink;
  }
  // 接口OK
  .green{
    color: lightgreen;
  }
  .delete {
    text-decoration: line-through;
    color: #eee;
  }
  #test {
    font-size: 14px;
    width: 100%;
  }
}
</style>



