export function dashDate(value) {
  if(!(value instanceof Date)) return;
  const formatObj = {
    y: value.getFullYear(),
    m: value.getMonth() + 1,
    d: value.getDate(),
  }
  formatObj.m < 10 ? formatObj.m = '0' + formatObj.m : '';
  formatObj.d < 10 ? formatObj.d = '0' + formatObj.d : ''; 
  return formatObj.y + ' - ' + formatObj.m + ' - ' + formatObj.d;
}


export function dashDateTime(value) {
  if(!(value instanceof Date)) return;
  let date = dashDate(value);
  let h = value.getHours();
  let m = value.getMinutes();
  let s = value.getSeconds();
  h < 10 ? h = '0' + h: '';
  m < 10 ? m = '0' + m: '';
  s < 10 ? s = '0' + s: '';

  return `${date} ${h} : ${m} : ${s}`;
}


