<template>
  <div id="five">
    <div class="nav-wp">
      <van-nav-bar title="选中终端设备" @click-left="goBack" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="./zong-fanhui.png" alt="" class="goback-btn">
        </div>
      </van-nav-bar>
      <!-- sub navbar -- search -->
      <van-tabbar v-model="subbarActive" class="sub-tabbar">
        <van-tabbar-item v-for="(item, index) in subbarList" :key="index" @click="subbarItemClick(item, index)">
          <span>{{item.activeItemValue || item.label}}</span>
          <div slot="icon" slot-scope="props" class="sub-tabbar-icon-wp">
            <img src="./xiala02.png" alt="" v-if="props.active">
            <img src="./xiala01.png" alt="" v-else>
          </div>
        </van-tabbar-item>
      </van-tabbar>
    </div>
    <no-result v-if="noResult"></no-result>
    <cube-index-list :data="equipmentData" @select="selectItem">
    </cube-index-list>
    <!-- 条件筛选 popup -->
    <van-popup v-model="subbarPopupShow" class="filter-popup" overlay-class="filter-overlay">
      <div class="item" v-for="(item, index) in filterList" :key="index" @click="subbarPopupItemClick(item, index)" :class="{active: subbarPopupItemActive === index}">{{item.label}}</div>
    </van-popup>
  </div>  
</template>

<script>
// 组件
import NoResult from '@/components/no-result/index';
// mixins
import commonApiMixin from '@/mixins/common-api-mixin';

const subbarList = [
  {label: '区域', type: 0, activeItemIndex: 0, activeItemValue: ''},
  {label: '线路', type: 1, activeItemIndex: 0, activeItemValue: ''},
];

export default {
  mixins: [commonApiMixin],
  data() {
    return {
      areaList: [],
      feederList: [],

      // subbar
      subbarList,
      subbarPopupShow: false,
      filterList: [],

      // 供点击开的subbar popup使用;
      subbarPopupItemActive: '',
      // TEST:
      equipmentData: [],
      subbarActive: -1,
    }
  },
  computed: {
    noResult() {
      return this.equipmentData.length ? false : true;
    }
  },
  methods: {
    goBack(){
      // this.$router.replace({path: '/'});
      // android go to index.html;
      webCtrl.finish()
    },
    selectItem(item) {
      this.$router.push({ name: 'FiveDetail', params: {id: item.value}})
    },
    subbarItemClick(item, index) {
      // 点击区域
      if(item.type === 0) {
        if(this.areaList.length) {
          this.filterList = this.areaList;
        }
        else {
          this.$toast('无区域列表');
          return;
        }
      }
      // 点击线路
      else if(item.type === 1) {
        if(this.feederList.length) {
          this.filterList = this.feederList;
        }
        else {
          this.$toast('无线路列表');
          return;
        }
      }
      this.subbarPopupItemActive = item.activeItemIndex;
      this.subbarPopupShow = true;
    },
    subbarPopupItemClick(item, index) {
      console.log(item);
      if(this.subbarActive === 0) {
        this.subbarList[0].activeItemIndex = index;
        this.subbarList[0].activeItemValue = item.label;
        
        this.subbarList[1].activeItemIndex = -1;
        this.subbarList[1].activeItemValue = '';
        
        this.equipmentData = [];
        this.getFeederByAreaId(item.id).then(() => {
          this.feederList.shift();
          this.subbarActive = 1;
          this.subbarItemClick(this.subbarList[1], 1);
        });
      } 
      else if(this.subbarActive === 1) {
        this.subbarList[1].activeItemIndex = index;
        this.subbarList[1].activeItemValue = item.label;

        this.getTrListByFeederId(item.id);
      }
      this.subbarPopupShow = false;
    },
    setDeafultSubbarLabel() {
      this.areaList.length ? this.subbarList[0].activeItemValue = this.areaList[0].label : '';
      this.feederList.length ? this.subbarList[1].activeItemValue = this.feederList[0].label: '';
    }
  },
  created() {
    this.getAreaList().then(() => {
      this.areaList.shift();
      if(!this.areaList.length) return;
      this.subbarList[0].activeItemValue = this.areaList[0].label;
      this.getFeederByAreaId(this.areaList[0].id).then(() => {
        this.feederList.shift();
        if(!this.feederList.length) return;
        this.subbarList[1].activeItemValue = this.stationList[0].label;
        this.setDeafultSubbarLabel();
        this.getTrListByFeederId(this.feederList[0].id);
      });
    });
  },
  components: {
    NoResult,
  }
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";
@import "@/common/scss/variable.scss";
#five{
  height: 100%;
  .nav-wp{
    height: 90px;
  }
  @extend %nav-with-one-title;
  @extend %sub-tabbar;
  @include subbar-popup(90px);
  .cube-index-list{
    height: calc(100% - 90px);
    .cube-index-list-anchor{
      margin-top: -1px;
      padding: 0 0 0 15px;
      height: 28px;
      line-height: 28px;
      font-size: 15px;
      color: #333;
      font-weight: bold;
      text-align: left;
    }
    .cube-index-list-item{
      @include border-1px(#dfdfdf);
      &::after{
        width: auto;
        right: 20px;
      }
      padding-left: 15px;
      font-size: 15px;
      text-align: left;
      color: #666;
      .cube-index-list-item-def{
        padding: 0;
      }
    }
    .cube-index-list-item:last-child{
      &::after{
        content: none;
      }
    }
    .cube-index-list-nav{
      .cube-index-list-nav-item{
        padding: 0 8px 6px 8px;
        font-size: 12px;
        color: $theme-color;
      }
    }
  }
}
</style>


