import originalJSONP from 'jsonp';
import {baseURL} from '@/common/const/common';

function param(data) {
  if(!data) {
    return '';
  }
  let url = '';
  for (let i in data) {
    let value = (data[i] !== undefined) ? data[i] : ''
    url += `&${i}=${encodeURIComponent(JSON.stringify(value))}`;
  }
  return url ? url.substring(1) : '';
}

export default function jsonpJSON(url, data, opts) {
  url += ((url.indexOf('?') === -1) ? '?' : '&') + param(data);
  url = baseURL + url;
  console.log(url,'xxxxx')
  return new Promise((resolve, reject) => {
    originalJSONP(url, opts, (err, data) => {
      if (!err) {
        resolve(data)
      } else {
        reject(err)
      }
    })
  })
}


