<template>
  <div id="confirmed-defect">
    <div class="nav-wp">
      <van-nav-bar title="缺陷管理" @click-left="goBack" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="../../zong-fanhui.png" alt="" class="goback-btn">
        </div>
      </van-nav-bar>
      <!-- 历史查询时间 -->
      <div class="history-search-wp">
        <div class="left-arrow-wp"  @click="prevTime">
          <img src="./lishi-left.png" alt="" class="left-arrow">
        </div>
        <span class="time-from" @click="pickFromTime">{{fromTime | dashDate}}</span>
        <span class="text">至</span>
        <span class="time-end" @click="pickEndTime">{{endTime | dashDate}}</span>
        <div class="right-arrow-wp" @click="nextTime">
          <img src="./lishi-right.png" alt="" class="right-arrow">
        </div>
      </div>
    </div>
    <no-result v-if="noResult"></no-result>
    <div class="main-wp" ref="mainWp" v-else>
      <div class="title-wp">
        <span class="title-item">序号</span>
        <span class="title-item">设备名称</span>
        <span class="title-item">缺陷等级</span>
        <span class="title-item">最迟消缺时间</span>
        <span class="title-item">消缺状态</span>
      </div>
      <div class="sticky-wp">
        <div class="table-main">
          <div v-for="(item, index) in curPageList" :key="index" class="table-item border-1px">
            <span class="item">{{itemIndexPri + index }}</span>
            <span class="item">{{item.devName}}</span>
            <span class="item">{{defectLevelList[item.defectLevel]}}</span>
            <span class="item">{{item.lastHandleTime}}</span>
            <span class="item">{{handleStateList[item.handleState]}}</span>
          </div>
        </div>
        <van-pagination class="pagination" v-model="curPageIndex" force-ellipses :total-items="totalLen" :items-per-page="itemsPerPage" :show-page-size="2"/>
      </div>
    </div>
    <div class="bottom-tabbar van-hairline--top">
      <div class="tabbar-item" @click="goFourIndex">
        <span>未确认缺陷</span>
      </div>
      <div class="split"></div>
      <div class="tabbar-item active">
        <span>已确认缺陷</span>
      </div>
    </div>
    <!-- 时间选择器 -->
    <van-popup v-model="timePickerShow" position="bottom">
      <van-datetime-picker v-model="timePicker.currentDate" type="date" :min-date="timePicker.minDate" :max-date="timePicker.maxDate" @confirm="pickTimeOk" @cancel="pickTimeCancel"/>
    </van-popup>
  </div>
</template>

<script>
// filters
import {dashDate} from '@/filters/index';
// components
import NoResult from '@/components/no-result/index';
// mixins
import apiMixin from '../../mixins/api-mixin';

const MIN_DATE = new Date(2018, 0, 1);
const MAX_DATE = new Date();

// 消缺状态
const handleStateList = {
  '0': '待派单',
  '1': '待接单',
  '2': '待消缺',
  '3': '待验收',
  '4': '已归档', 
}

// 缺陷类型
const errTypeObj = {
  '5': '终端长期离线',
  '6': '终端频繁投退',
  '7': '开关遥控失败',
  '8': '信号抖动',
  '9': '数据缺漏异常',
  '10': '电源故障',
  '11': '蓄电池故障',
  '12': '流量越限',
  '99': '其他',
};

// 缺陷等级
const defectLevelList = {
  '1': '一般',
  '2': '紧急'
}

export default {
  mixins: [apiMixin],
  components: {
    NoResult,
  },
  data() {
    return {
      // api-mixin getHisDefectInfo使用;
      // default：0 代表所有
      feederId: 0,
      // default: 0 代表所有
      defectType: 0,
      // 不可更改，1代表本页面查询的是已确认缺陷
      ifConfirm: 1,
      fromTime: new Date(),
      endTime: new Date(),

      tableList: [],

      handleStateList,
      defectLevelList,
      errTypeObj,

      timePickerShow: false,
      timePicker: {
        // from, end
        type: 'from',
        currentDate: new Date(),
        minDate: MIN_DATE,
        maxDate: MAX_DATE,
      },

      // 分页
      curPageIndex: 1,
      itemsPerPage: 10,
    }
  },
  computed: {
    // 分页
    totalLen() {
      return this.tableList.length;
    },
    curPageList() {
      return this.tableList.slice(this.itemsPerPage * (this.curPageIndex - 1), this.itemsPerPage * this.curPageIndex);
    },
    itemIndexPri() {
      return (this.curPageIndex - 1) * this.itemsPerPage + 1;
    },
    noResult() {
      return this.tableList.length ? false : true;
    }
  },
  methods: {
    goBack(){
      this.$router.replace({path: '/four'});
    },
    goFourIndex(){
      this.$router.replace({path: '/four'});
    },
    // 前一天
    prevTime() {
      this.fromTime = this.endTime = new Date(this.fromTime.getTime() - 86400 * 1000);
      this.RefreshHisTableList();
    },
    // 后一天
    nextTime() {
      this.fromTime = this.endTime = new Date(this.endTime.getTime() + 86400 * 1000);
      this.RefreshHisTableList();
    },
    RefreshHisTableList() {
      this.tableList = [];
      this.getDefectHisInfo();
    },
    pickFromTime() {
      this.timePicker.minDate = MIN_DATE;
      this.timePicker.maxDate = MAX_DATE;
      this.timePicker.currentDate = new Date();
      this.timePicker.type = 'from';
      this.timePickerShow = true;
    },
    pickEndTime() {
      // this.timePicker.minDate = this.fromTime;
      this.timePicker.currentDate = this.endTime;
      this.timePicker.type = 'end';
      this.timePickerShow = true;
    },
    pickTimeOk(value) {
      if(this.timePicker.type === 'from') {
        this.fromTime = value;
      } else if(this.timePicker.type === 'end'){
        this.endTime = value;
        this.timePicker.minDate = MIN_DATE;
        this.timePicker.maxDate = MAX_DATE;
      }
      this.timePickerShow = false;
      this.RefreshHisTableList();
    },
    pickTimeCancel() {
      this.timePickerShow = false;
    },
    resizeMainH() {
      this.$nextTick(() => {
        let documentH = document.documentElement.clientHeight || document.body.clientHeight;
        let bottomTabbarH = 47.5;
        let navH = 93;
        let mainH = documentH - navH - bottomTabbarH + 'px';
        if(!this.$refs['mainWp']) return;
        this.$refs['mainWp'].style.height = mainH;
      })
    },
  },
  watch: {
    noResult(value) {
      if(!value) {
        if(this.tabbarActive === 0) {
          this.resizeMainH();
        } 
        else if(this.tabbarActive === 1) {
          this.resizeMainH(1);
        }
      }
    }
  },
  created() {
    this.getDefectHisInfo();
  },
  mounted() {
    this.resizeMainH();
  },
  filters: {
    dashDate
  }
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#confirmed-defect{
  height: 100%;
  .nav-wp{
    height: 93px;
  }
  .history-search-wp{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f3f3f3;
    height: 45px;
    font-size: 15px;
    color: #1bbc9b;
    .left-arrow-wp,
    .right-arrow-wp{
      display: flex;
      align-items: center;
      position: absolute;
      padding: 0 10px;
      height: 45px;
    }
    .left-arrow-wp{
      left: 15px;
    }
    .right-arrow-wp{
      right: 15px;
    }
    .left-arrow,
    .right-arrow{
      width: 9.5px;
      height: 17px;
    }
    .text{
      padding: 0 30px;
      color: #666;
    }
  }
  @extend %nav-with-one-title;
  .bottom-tabbar {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 47.5px;
    line-height: 47.5px;
    font-size: 0;
    color: #333;
    background: #fff;
    &::after{
      border-top-color: #999;
    }
    .active{
      color: #1abd9b;
    }
    .tabbar-item{
      display: inline-block;
      width: 50%;
      font-size: 15px;
      text-align: center;
    }
    .split{
      position: absolute;
      top: 50%;
      left: 50%;
      height: 20px;
      width: 0px;
      transform: translate(-50%, -50%);
      border-left: 1px solid #999;
    }
  }
  .main-wp{
    height: calc(100% - 140.5px);
    .sticky-wp{
      height: calc(100% - 47.5px);
      overflow: auto;
      background-color: #f3f3f3;
      .table-main{
        min-height: 100%;
        padding-bottom: 40px;
        // height: calc(100% - 40px);
        // overflow-y: auto;
      }
      .table-item{
        padding: 18px 0;
        background-color: #fff;
        @include border-1px(#dcdcdc);
        font-size: 0;
        .item {
          line-height: 1.5;
          vertical-align: middle;
        }
        .item:nth-child(n+1){
          display: inline-block;
          width: 20%;
          font-size: 14px;
          text-align: center;
        }
        .item:first-child{
          width: 12%;
        }
        .item:nth-child(4){
          width: 25%;
        }
      }
    }
    .pagination{
      margin-top: -40px;
    }
    .title-wp{
      padding: 14px 0;
      line-height: 1.5;
      font-size: 0;
      color: #fff;
      background-color: #33d8b6;
      .title-item{
        font-size: 14px;
        text-align: center;
      }
      .title-item:nth-child(n+1){
        display: inline-block;
        width: 21%;
      }
      .title-item:first-child{
        width: 12%;
      }
      .title-item:nth-child(4){
        width: 25%;
      }
    }
   
  }
  // .van-popup{
  //   width: 100%;
  // }
  // .van-picker{
  //   position: fixed;
  //   width: 100%;
  //   bottom: 0;
  // }
  @extend %pagination;
}
</style>



