import $ from "jquery";
window.$ = $;
import Tile from "ol/layer/tile";

import extent from 'ol/extent';

import proj from "ol/proj";
import GeoJSON from "ol/format/geojson";
import WMTS from 'ol/tilegrid/wmts';
import WMTS2 from 'ol/source/wmts';

export default {
  data() {
    return {
      response: '',
    }
  },
  methods: {
    addWMTSLayer(url, mapServiceName, map) {
      this.GetVendorWMTSCapabilities(url, mapServiceName, map);
    },
    GetVendorWMTSCapabilities(url, mapServiceName, map) {
      let request = "SERVICE=WMTS&REQUEST=getcapabilities&TILED=true";
      if (!this.response) {
        let xhr = new XMLHttpRequest();
        xhr.open('GET', `${url}?${request}`);
        xhr.send();
        xhr.onload = (e) => {
          this.response = xhr.response;
          this.loadLayerFromParameter(this.response, url, mapServiceName, map);
        };
        return;
      }
      this.loadLayerFromParameter(this.response, url, mapServiceName, map);
    },
    loadLayerFromParameter(capabilitiesResponse, uRLServer, mapServiceName, map) {
      // // Get All Layers from GetCapabilities
      var Layers = $(capabilitiesResponse).find("Layer");
      var i = this.getLayerIdx(Layers, mapServiceName);
      var myFormat = $(Layers[i]).find("Format").text();
      var TileMatrix = $(Layers[i]).find("TileMatrix");

      let TileMatrixIds = [];
      for (let t = 0; t < TileMatrix.length; t++) {
        TileMatrixIds.push($(TileMatrix[t]).text());
      }

      let myTileMatrixSet = $(Layers[i]).find("TileMatrixSet").text();
      let epsg_projection = this.getProjection(capabilitiesResponse, myTileMatrixSet);
      let projection = proj.get(epsg_projection);
      let projectionExtent = projection.getExtent();

      var tileSizeListe = this.getTileSizes(capabilitiesResponse, myTileMatrixSet);
      var origines = this.getOrigines(capabilitiesResponse, myTileMatrixSet);

      // // Retrieve 256 as TileSize in the GetCapabilities
      var widthE = extent.getWidth(projectionExtent);

      var nbReso = TileMatrixIds.length;
      var resolutions = new Array(nbReso);
      var matrixIds = new Array(nbReso);
      for (var z = 0; z < nbReso; ++z) {
        // generate resolutions and matrixIds arrays for this WMTS
        var size = widthE / tileSizeListe[z];
        resolutions[z] = size / Math.pow(2, z);
      }

      /* Not Use Yet */
      var TileMatrixLimits = $(Layers[i]).find("TileMatrixLimits");
      this.getTileMatrixLimit(TileMatrixLimits, TileMatrixIds[1]);
      var tileGrg = new WMTS({
        origins: origines,
        resolutions: resolutions,
        matrixIds: TileMatrixIds,
        tileSizes: tileSizeListe
      });
      var WMTSsource = new WMTS2({
        url: uRLServer,
        layer: mapServiceName,
        matrixSet: myTileMatrixSet,
        format: myFormat,
        projection: projection,
        tileGrid: tileGrg,
        style: 'default'
      });
      var tileLayer = new Tile({
        extent: projectionExtent,
        source: WMTSsource,
        zIndex: -1
      });
      this.map.addLayer(tileLayer);
    },
    getProjection (capabilitiesResponse, myTileMatrixSet) {
      var epsgCode;
      var TileMatrixSet = $(capabilitiesResponse).find("TileMatrixSet");
      for (var i = 0; i < TileMatrixSet.length; i++) {
        var TileMatrixSetName = this.getNameInNodeList(TileMatrixSet, i, "OWS:IDENTIFIER");
        if (TileMatrixSetName == myTileMatrixSet) {
          var supportedCRS = this.getNameInNodeList(TileMatrixSet, i, "OWS:SUPPORTEDCRS");
          var codeEPSG = supportedCRS.split(':');
          return epsgCode = "EPSG:" + codeEPSG[codeEPSG.length - 1];
        }
      }
      return "EPSG:3857";
    },
    getLayerIdx(Layer, mapServiceName) {
      for (var i = 0; i < Layer.length; i++) {
        var layerName = this.getNameInNodeList(Layer, i, "OWS:IDENTIFIER");
        var sublayers = $(Layer[i]).find("Layer");
        if (sublayers.length == 0 && layerName == mapServiceName)
          return i;
      }
      return -1
    },
    getNameInNodeList(nodeList, i, nodeName) {
      var child = $(nodeList[i]).children();
      for (var j = 0; j < child.length; j++) {
        if ($(child[j]).context.nodeName == nodeName)
          return $(child[j]).text();
      }
      return "Null";
    },
    getTileSizes(capabilitiesResponse, myTileMatrixSet) {
      var TileSizeList = [];
      var TileMatrixSet = $(capabilitiesResponse).find("TileMatrixSet");
      for (var i = 0; i < TileMatrixSet.length; i++) {
        var TileMatrixSetName = this.getNameInNodeList(TileMatrixSet, i, "OWS:IDENTIFIER");
        if (TileMatrixSetName == myTileMatrixSet) {
          var tms = $(TileMatrixSet[i]).find('TileWidth');
          for (var k = 0; k < tms.length; k++) {
            var b = parseInt($(tms[k]).text());
            TileSizeList.push(b);
          }
        }
      }
      return TileSizeList;
    },
    getOrigines(capabilitiesResponse, myTileMatrixSet) {
      var origins = [];
      var TileMatrixSet = $(capabilitiesResponse).find("TileMatrixSet");
      for (var i = 0; i < TileMatrixSet.length; i++) {
        var TileMatrixSetName = this.getNameInNodeList(TileMatrixSet, i, "OWS:IDENTIFIER");
        if (TileMatrixSetName == myTileMatrixSet) {
          var tms = $(TileMatrixSet[i]).find('TopLeftCorner');
          for (var k = 0; k < tms.length; k++) {
            var ori = $(tms[k]).text().split(' ');
            var left = parseFloat(ori[0]);
            var top = parseFloat(ori[1]);
            origins.push([left, top]);
          }
        }
      }
      return origins;
    },
    getTileMatrixLimit(TileMatrixLimits, name) {
      var TileLimitList = [];
      for (var i = 0; i < TileMatrixLimits.length; i++) {
        var myTileMatrix = $(TileMatrixLimits[i]).find("TileMatrix").text();
        if (name == myTileMatrix) {
          var MinTileRow = $(TileMatrixLimits[i]).find("MinTileRow").text();
          var MinTileCol = $(TileMatrixLimits[i]).find("MinTileCol").text();
          var MaxTileRow = $(TileMatrixLimits[i]).find("MaxTileRow").text();
          var MaxTileCol = $(TileMatrixLimits[i]).find("MaxTileCol").text();
          TileLimitList = [MinTileRow, MinTileCol, MaxTileRow, MaxTileCol];
        }
      }
      return TileLimitList;
    }
  }
}



