<template>
  <div id="one">
    <div class="nav-wp" ref="navWp">
      <van-nav-bar title="故障研判" @click-left="goBack" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="./zong-fanhui.png" alt="" class="goback-btn">
        </div>
      </van-nav-bar>
      <div class="subbar">
        <div class="item" @click="todayGZClick">
          <span class="title">当日故障</span>
        </div>
        <div class="item" @click="toggleSubbarPopup">
          <span class="title">条件查询</span>
          <img src="./xiala.png" alt="" class="img">
        </div>
      </div>
      <div class="search-overview" v-show="!isTodayGZ">
        <img src="./select.png" alt="" class="icon">
        <span class="title">条件查询数据<span class="value">{{gzList.length}}</span>条</span>
      </div>
      <ul class="falut-type-info">
        <li class="item">开关跳闸</li>
        <li class="item">离线FA</li>
        <li class="item">接地动作</li>
        <li class="item">配变失电</li>
      </ul>
    </div>
    <no-result v-if="noResult"></no-result>
    <div class="main-wp" ref="mainWp" v-else>
      <div class="con-wp">
        <template v-for="(item, index) in curPageList">
          <div class="main-item" :key="index" @click="goMap(item)">
            <div class="line-wp">
              <span class="title">故障线路：</span>
              <span class="text">{{item.gzxl}}</span>
            </div>
            <div class="time-wp">
              <span class="title">故障时间：</span>
              <span class="text">{{item.gzsj}}</span>
            </div>
            <indicator :gzItem="item"></indicator>
          </div>
          <split-view></split-view>
        </template>
      </div>
      <van-pagination class="pagination" v-model="curPageIndex" force-ellipses :total-items="totalLen" :items-per-page="itemsPerPage" v-if="totalLen" :show-page-size="2"/>
    </div>
    
    <!-- navbar popup -->
    <van-popup v-model="subbarPopupShow" class="subbar-popup" overlay-class="overlay">
      <ul>
        <li class="item border-1px" @click="pickFromTime">
          <span class="label">起始时间:</span>
          <span>{{fromTime | dashDate}}</span>
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </li>
        <li class="item border-1px" @click="pickEndTime">
          <span class="label">终止时间:</span>
          <span>{{endTime | dashDate}}</span>
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </li>
        <li class="item border-1px" @click="pickSubstation">
          <span class="label">变电站:</span>
          <span>{{substationName}}</span>
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </li>
        <li class="item border-1px" @click="pickLine">
          <span class="label">配电线路:</span>
          <span>{{lineName}}</span>
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </li>
        <li class="item border-1px" @click="pickErrType">
          <span class="label">故障类型:</span>
          <span>{{errType}}</span>
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </li>
        <li class="item border-1px" @click="pickStatusType">
          <span class="label">研判结果:</span>
          <span>{{status}}</span>
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </li>
        <li class="cofirm-btn" @click="search">
          <span class="btn">条件搜索</span>
        </li>
      </ul>
    </van-popup>
    <!-- 时间选择器 -->
    <van-popup v-model="timePickerShow" position="bottom" class="timepicker-popup">
      <van-datetime-picker v-model="timePicker.date" type="date" :min-date="timePicker.minDate" :max-date="timePicker.maxDate" @confirm="pickTimeConfirm" @cancel="pickTimeCancel"/>
    </van-popup>
    <!-- 变电站选择 -->
    <van-popup v-model="substationPickerShow" position="bottom" class="">
      <van-picker :columns="stationList" value-key="label" @confirm="substationPickConfirm" @cancel="substationPickCancel" :show-toolbar="true"/>
    </van-popup>
    <!-- 配电线路选择 -->
    <van-popup v-model="linePickerShow" position="bottom" class="">
      <van-picker :columns="feederList" value-key="label" @confirm="linePickConfirm" @cancel="linePickCancel" :show-toolbar="true"/>
    </van-popup>
    <!-- 故障选择 -->
    <van-popup v-model="errTypePickerShow" position="bottom" class="">
      <van-picker :columns="errTypeList" value-key="name" @confirm="errTypePickConfirm" @cancel="errTypePickCancel" :show-toolbar="true"/>
    </van-popup>
    <!-- 研判结果选择 -->
    <van-popup v-model="statusTypePickerShow" position="bottom" class="">
      <van-picker :columns="statusTypeList" value-key="name" @confirm="statusTypePickConfirm" @cancel="statusTypePickCancel" :show-toolbar="true"/>
    </van-popup>
  </div>  
</template>

<script>
//util
import _util from '@/util/index';
// api
import * as fdApi from '@/api/fault_determine';
// 组件
import SplitView from '@/components/split-view';
import Indicator from './components/indicator/index';
import NoResult from '@/components/no-result/index';
// mixin
import commonApiMixin from '@/mixins/common-api-mixin';
// filters
import {dashDate} from '@/filters';
// const variable;
// ORG常州地区Id
import {ORG} from '@/common/const/common';

const MIN_DATE = new Date(2018, 0, 1);
const MAX_DATE = new Date();

// 选择开始时间的TYPE
const FROM_TIME = 0;
// 选择结束时间的TYPE
const END_TIME = 1;

const errTypeList = [
  {
    name: '有效故障',
    value: 0
  },
  {
    name: '非故障',
    value: 1
  }
]

const statusTypeList = [
  {
    name: '开关跳闸',
    value: 0
  },
  {
    name: '离线FA',
    value: 1
  },
  {
    name: '接地故障',
    value: 2
  },
  {
    name: '配变失电',
    value: 3
  },
]

// 当日故障请求参数
const todayReq = {
  org: ORG,
  bdz: '',
  line: '',
  start_time: _util.dashDateWithoutSpace(new Date()),
  end_time:  _util.dashDateWithoutSpace(new Date()),
  status: '',
  gzlx: '',
};

export default {
  mixins: [commonApiMixin],
  data() {
    return {
      todayReq,
      isTodayGZ: true,
      gzList: [],
      curPageIndex: 1,
      itemsPerPage: 10,

      subbarPopupShow: false,
      substationName: '',
      lineName: '',
      statusType: '',      
      errType: '',
      
      org: ORG,
      substationId: '',
      lineId: '',
      fromTime: '',
      endTime: '',
      status: '',
      errTypeId: '',

      timePickerShow: false,
      timePicker: {
        type: '',
        date: '',
        minDate: MIN_DATE,
        maxDate: MAX_DATE
      },

      stationList: [],
      feederList: [],
      errTypeList,
      statusTypeList,
      
      substationPickerShow: false,
      linePickerShow: false,
      errTypePickerShow: false,
      statusTypePickerShow: false,
    }
  },
  computed: {
    totalLen() {
      return this.gzList.length;
    },
    curPageList() {
      return this.gzList.slice(this.itemsPerPage * (this.curPageIndex - 1), this.itemsPerPage * this.curPageIndex);
    },
    noResult() {
      return this.gzList.length ? false : true;
    }
  },
  methods: {
    goBack(){
      // this.$router.replace({path: '/'});
      // android go to index.html;
      webCtrl.finish()
    },
    // 点击当日故障
    todayGZClick() {
      this.isTodayGZ = true;
      this.subbarPopupShow = false;
      this.resizeMainH();
      
      this.getFailureList(this.todayReq);
    },
    // 跳转到map页
    goMap(item) {
      // console.log(item);
      // let params = {
      //   faultId: item.id,
      //   tzkg: item.tzkgId,
      //   ssls: this.isTodayGZ ? 'ss' : 'ls',
      // }
      this.$router.push({name: 'oneMap', params: item})
    },
    toggleSubbarPopup() {
      this.subbarPopupShow = !this.subbarPopupShow;
    },
    // 选取开始时间
    pickFromTime() {
      this.timePicker.type = FROM_TIME;
      this.timePickerShow = true;
    },
    // 选取结束时间
    pickEndTime() {
      this.timePicker.type = END_TIME;
      this.timePickerShow = true;
    },
    // timepicker 确认
    pickTimeConfirm(value) {
      if(this.timePicker.type === FROM_TIME) {
        this.fromTime = value;
      } else if(this.timePicker.type === END_TIME) {
        this.endTime = value;
      }
      this.timePickerShow = false;
    },
    // timepicker 取消
    pickTimeCancel() {
      this.timePickerShow = false;
    },
    // 选取变电站
    pickSubstation() {
      if(this.stationList.length) {
        this.substationPickerShow = true;
      } else {
        this.$toast('未获取到变电站列表');
      }
    },
    // 选取变电站 确认
    substationPickConfirm(item) {
      this.substationName = item.label;
      this.substationId = item.id;
      this.lineName = '';
      this.lineId = '';
      this.substationPickerShow = false;
      this.getFeederByAreaId(null, this.substationId).then(() => {
        this.feederList.shift();
        if(this.feederList && !this.feederList.length) {
          this.$toast('查询不到线路');
        }
      });
    },
    // 选取变电站 取消
    substationPickCancel() {
      this.substationPickerShow = false;
    },
    // 线路
    pickLine() {
      if(!this.substationId) {
        this.$toast('请先选择变电站');
        return;
      }
      // 如果feederList length = 0;
      else if(!this.feederList.length) {
        this.$toast('无线路可选');
        return;
      }
      this.linePickerShow = true;
    },
    linePickConfirm(item) {
      this.lineName = item.label;
      this.lineId = item.id;
      this.linePickerShow = false;
    },
    linePickCancel() {
      this.linePickerShow = false;
    },

    // 故障类型
    pickErrType() {
      this.errTypePickerShow = true;
    },
    errTypePickConfirm(item) {
      this.errType = item.name;
      this.errTypeId = item.value;
      this.errTypePickerShow = false;
    },
    errTypePickCancel() {
      this.errTypePickerShow = false;
    },
    
    // 研判结果选择
    pickStatusType() {
      this.statusTypePickerShow = true;
    },
    statusTypePickConfirm(item) {
      this.status = item.name;
      this.statusType = item.value;
      this.statusTypePickerShow = false;
    },
    statusTypePickCancel() {
      this.statusTypePickerShow = false;
    },
    
    // 条件搜索Btn
    search() {
      let req = {
       org: ORG,
       bdz: this.substationId,
       line: this.lineId,
       start_time: _util.dashDateWithoutSpace(this.fromTime),
       end_time: _util.dashDateWithoutSpace(this.endTime),
       status: this.statusType,
       gzlx: this.errTypeId,
      }
      if(!req.start_time || !req.end_time) {
        this.$toast('请选择开始和结束时间');
      }
      else if(this.fromTime.getTime() > this.endTime.getTime()) {
        this.$toast('开始时间大于结束时间');
      }
      else {
        this.isTodayGZ = false;
        this.resizeMainH();
        this.getFailureList(req);
      }
      this.subbarPopupShow = false;
    },
    resizeMainH() {
      let documentH = document.documentElement.clientHeight || document.body.clientHeight;
      if(!this.$refs['mainWp']) return;
      let navHeight = this.$refs['navWp'].getBoundingClientRect().height;
      this.$refs['mainWp'].style.height = documentH - navHeight + 'px';
    },
    // 获取故障研判列表
    getFailureList(req) {
      this.gzList = [];
      this.curPageIndex = 1;
      fdApi.getFailureList(req).then(res => {
        let data = res[0].lsgz;
        this.gzList = data;
        if(!this.gzList.length) {
          this.$toast('无故障数据')
        }
      }).catch(err => {
        console.log(err);
      })
    }
  },
  filters: {
    dashDate
  },
  components: {
    SplitView,
    Indicator,
    NoResult
  },
  created() {
    // 获取当日故障列表
    this.getFailureList(this.todayReq);
    // 获取地区列表
    this.getSubstationByAreaId().then(() => {
      this.stationList.shift();
    });
  },
  mounted() {
    this.resizeMainH();
  }
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#one{
  display: flex;
  flex-direction: column;
  height: 100%;
  @extend %nav-with-one-title;
  @extend %pagination;
  .nav-wp{
    // height: 123px;
  }
  .subbar {
    position: relative;
    height: 45px;
    background-color: #038469;
    font-size: 0;
    text-align: center;
    .item{
      display: inline-block;
      width: 50%;
      line-height: 45px;
      font-size: 14px;
      color: #fff;
      .img{
        width: 6.5px;
        height: 4px;
        vertical-align: middle;
      }
      &:first-child{
        &::after{
          content: '';
          position: absolute;
          left: 50%;
          top: 50%;
          width: 1px;
          height: 30px;
          background-color: #1bbc9b;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
  .subbar-popup{
    box-sizing: border-box;
    margin: 0 15px;
    width: calc(100% - 30px);
    left: 0;
    top: 106px;
    padding: 0 10px;
    border-radius: 3px;
    transform: translate(0, 0);
    background-color: #fff;
    .item{
      padding-left: 6px;
      height: 60px;
      line-height: 60px;
      text-align: left;
      color: #666;
      @include border-1px(#c8c7cb);
      &:last-child{
        @include border-none;
      }
      .label{
        margin-right: 5px;
      }
      .right-arrow{
        box-sizing: content-box;
        position: absolute;
        right: -7.5px;
        top: 50%;
        padding: 24px 15px 24px 24px;
        width: 6.5px;
        height: 11.5px;
        transform: translateY(-50%);
      }
    }
    .cofirm-btn{
      display: block;
      margin: 30px 0 37.5px 0;
      padding: 13px 0;
      font-size: 15px;
      text-align: center;
      color: #fff;
      background: $theme-color;
      border-radius: 3px;
      cursor: pointer;
    }
    &::before{
      content: '';
      position: absolute;
      top: -5px;
      right: 14.6%;
      height: 10px;
      width: 10px;
      background: #fff;
      transform:rotate(45deg);
    }
  }
  .search-overview{
    padding: 3px 0 0 15px;
    height: 31px;
    text-align: left;
    color: #666;
    background: #eee;
    .icon{
      margin-right: 14px;
      width: 12.5px;
      height: 14px;
    }
    .value{
      padding: 0 4px;
      line-height: 31px;
      color: #038469;
    }
  }
  .falut-type-info{
    padding-left: 32px;
    height: 30px;
    line-height: 30px;
    font-size: 12px;
    text-align: left;
    color: #666;
    background: #eee;
    .item{
      position: relative;
      display: inline-block;
      width: 22%;
      &::before{
        content: '';
        position: absolute;
        left: -15px;
        top: 50%;
        padding-right: 4px;
        transform: translateY(-50%);
        height: 7px;
        width: 7px;
        background: #eee;
        border-radius: 7px;
      }
      &:nth-child(1) {
        &::before{
          background: #e95c07;
        }
      }
      &:nth-child(2) {
        &::before{
          background: #008ad3;
        }
      }
      &:nth-child(3) {
        &::before{
          background: #b654d2;
        }
      }
      &:nth-child(4) {
        &::before{
          background: #2bbc90;
        }
      }
    }
  }
  .overlay{
    top: 92px;
    background-color: rgba(0, 0, 0, 0.2) 
  }
  .main-wp{
    overflow-y: auto;
    flex: 1;
    background: #fff;
    .con-wp{
      min-height: 100%;
      padding-bottom: 40px;
    }
    .pagination{
      margin-top: -40px;
    }
  }
  .main-item{
    position: relative;
    padding-left: 17px;
    height: 84px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #fff;
    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      margin-top: 15px;
      height: 54px;
      width: 5px;
      background: #1bbc9b;
    }
    .line-wp{
      padding-top: 15px;
      font-size: 18px;
      color: #333;
    }
    .time-wp{
      padding-bottom: 15px;
      font-size: 15px;
      color: #666;
    }
    
  }

  // .timepicker-popup {
  //   width: 100%;
  // }
  // .van-picker{
  //   position: fixed;
  //   width: 100%;
  //   bottom: 0;
  // }
}
</style>



