<template>
  <div id="six">
    <div class="nav-wp">
      <van-nav-bar title="终端台账" @click-left="goBack" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="./zong-fanhui.png" alt="" class="goback-btn">
        </div>
      </van-nav-bar>
      <!-- search-input -->
      <div class="search-wp">
        <van-cell-group>
          <van-field v-model.trim="terminalName" @input="searchTerminal"/>
          <div class="placeholder-wp" v-if="showPlaceholder">
            <van-icon name="search" class="search-icon"></van-icon>
            <span class="placeholder">请输入终端名称</span>
          </div>
        </van-cell-group>
      </div>
      <!-- sub navbar -- filter -->
      <van-tabbar v-model="subbarActive" class="sub-tabbar" :fixed="false">
        <van-tabbar-item v-for="(item, index) in subbarList" :key="index" @click="subbarItemClick(item, index)">
          <span>{{item.activeItemValue || item.label}}</span>
          <div slot="icon" slot-scope="props" class="sub-tabbar-icon-wp">
            <img src="./xiala02.png" alt="" v-if="props.active">
            <img src="./xiala01.png" alt="" v-else>
          </div>
        </van-tabbar-item>
      </van-tabbar>
    </div>
    <split-view></split-view>
    <div class="main-wp">
      <div class="title-wp">
        <span class="title-item">终端名称</span>
        <span class="title-item">终端型号</span>
        <span class="title-item">通信方式</span>
        <span class="title-item">通道工况</span>
      </div>
      <no-result v-if="noResult"></no-result>
      <div class="sticky-wp" v-else>
        <div class="table-main">
          <div v-for="(item, index) in curPageList" :key="index" class="table-item border-1px">
            <router-link :to="{ name: 'SixDetail', params: {item}}" tag="div" class="item-wp">
              <span class="item">{{item.a}}</span>
              <span class="item">{{item.b}}</span>
              <span class="item">{{item.c}}</span>
              <span class="item">{{item.d}}</span>
            </router-link>
          </div>
        </div>
        <van-pagination class="pagination" v-model="curPageIndex" force-ellipses :total-items="totalLen" :items-per-page="itemsPerPage" v-if="totalLen" :show-page-size="2"/>
      </div>
    </div>
    <!-- 条件筛选 popup -->
    <van-popup v-model="subbarPopupShow" class="filter-popup" overlay-class="filter-overlay">
      <span class="item" v-for="(item, index) in filterList" :key="index" @click="subbarPopupItemClick(item, index)" :class="{active: subbarPopupItemActive === index}">{{item.label}}</span>
    </van-popup>
  </div>
</template>

<script>
// import 'vant/lib/vant-css/icon-local.css';
// 组件
import SplitView from '@/components/split-view/index';
import NoResult from '@/components/no-result/index';
// mixin
import commonApiMixin from '@/mixins/common-api-mixin';
import apiMixin from './mixins/api-mixin';

// 终端台账API
import * as zdtzApi from '@/api/zdtaizhang';

import {channelConditionTypeObj, comMethodObj, deviceTypeObj} from '@/common/const/dztz';

const subbarList = [
  {label: '区域', type: 0, activeItemIndex: 0, activeItemValue: ''},
  {label: '变电站', type: 1, activeItemIndex: 0, activeItemValue: ''},
  {label: '线路', type: 2, activeItemIndex: 0, activeItemValue: ''},
  {label: '终端类型', type: 3, activeItemIndex: 0, activeItemValue: ''},
];

export default {
  mixins: [commonApiMixin, apiMixin],
  data() {
    return {
      areaList: [],
      stationList: [],
      feederList: [],
      terminalTypeList: [],

      areaId: '',
      feederId: '',
      stationId: '',
      terminalTypeId: '',

      // subbar
      subbarList,
      subbarPopupShow: false,
      filterList: [],
      // subbar被active的项；
      subbarActive: -1,
      // subbar点击后下拉框中active的项;
      subbarPopupItemActive: '',


      terminalName: '',
      focus: false,
      tableList: [],
      showPlaceholder: true,

      // 分页
      curPageIndex: 1,
      itemsPerPage: 10,

      allTableList: [],
    }
  },
  computed: {
    // 分页
    totalLen() {
      return this.tableList.length;
    },
    curPageList() {
      return this.tableList.slice(this.itemsPerPage * (this.curPageIndex - 1), this.itemsPerPage * this.curPageIndex);
    },
    noResult() {
      return this.tableList.length ? false : true;
    }
  },
  watch: {
    terminalName: {
      handler(value) {
        if(value) {
          this.showPlaceholder = false;
        } else {
          this.showPlaceholder = true;
        }
      },
      immediate: true
    }
  },
  methods: {
    goBack(){
      // this.$router.replace({path: '/'});
      // android go to index.html;
      webCtrl.finish()
    },
    subbarItemClick(item, index) {
      // console.log(item);
      // 点击区域
      if(item.type === 0) {
        this.filterList = this.areaList;
      }
      // 点击变电站
      else if(item.type === 1) {
        this.filterList = this.stationList;
      }
      // 点击线路
      else if(item.type === 2) {
        if(!this.feederList.length) {
          this.$toast('没有线路信息');
          return;
        }
        this.filterList = this.feederList;
      }
      // 点击终端类型
      else if(item.type === 3) {
        if(this.subbarList[2].activeItemIndex < 0) {
          this.$toast('请先选择线路');
          this.subbarPopupShow = false;
          return false;
        } else {
          this.filterList = this.terminalTypeList;
        }
      }
      this.subbarPopupItemActive = item.activeItemIndex;
      this.subbarPopupShow = true;
    },
    subbarPopupItemClick(item, index) {
      // console.log(item, index);
      this.subbarList[this.subbarActive].activeItemIndex = index;
      // 区域
      if(this.subbarActive === 0) {
        this.areaId = item.id;
        this.subbarList[0].activeItemValue = item.label;
        // station
        this.subbarList[1].activeItemIndex = -1;
        this.subbarList[1].activeItemValue = '';
        // feeder
        this.subbarList[2].activeItemIndex = -1;
        this.subbarList[2].activeItemValue = '';
        
        this.tableList = [];

        this.getSubstationByAreaId(item.id).then(() => {
          this.stationList.shift();
          if(this.stationList.length) {
            this.subbarActive = 1;
            this.subbarItemClick(this.subbarList[1], 1);
          } else {
            this.$toast('此区域下无变电站')
          }
        });
      } 
      // 变电站
      else if(this.subbarActive === 1) {
        this.stationId = item.id;
        
        this.subbarList[1].activeItemValue = item.label;

        this.subbarList[2].activeItemIndex = -1;
        this.subbarList[2].activeItemValue = '';

        this.getFeederByAreaId(this.areaId, this.stationId).then(() => {
          this.feederList.shift();
          if(this.feederList.length) {
            this.subbarActive = 2;
            this.subbarItemClick(this.subbarList[2], 2);
          } else {
            this.$toast('此变电站下无线路')
          }
        });
      }
      // 线路
      else if(this.subbarActive === 2) {
        this.feederId = item.id;
        this.subbarList[2].activeItemValue = item.label;


        // 先清空当前，再请求；
        // this.terminalTypeList = [];
        // this.getTermTypes(item.id);
        this.getTermTableList();
      }
      // 终端类型
      else if(this.subbarActive === 3) {
        this.terminalTypeId = item.id;
        this.subbarList[3].activeItemValue = item.label;

        this.tableList = [];
        // 加载表格数据
        this.getTermTableList();
      }
      this.subbarPopupShow = false;
    },
    getTermTableList() {
      let req = {
        feederId: this.feederId || 0,
        termType: this.terminalTypeId || -1
      };
      if(!req.feederId) return;
      zdtzApi.getTermDataGridApi(req).then(res => {
        // let data = res.data;
        let data = res;
        if(data.length) {
          let tmp = [];
          data.forEach(item => {
            let obj = {};
            obj.id = item[0];
            obj.a = item[1];
            obj.b = deviceTypeObj[item[2]] ? deviceTypeObj[item[2]] : '';
            obj.c = comMethodObj[item[4]] ? comMethodObj[item[4]] : '';
            obj.d = channelConditionTypeObj[item[5]] ? channelConditionTypeObj[item[5]] : '';
            tmp.push(obj);
          })
          this.tableList = tmp;
        } else {
          this.tableList = [];
          this.$toast('获取到的终端列表为空')
        }
        this.curPageIndex = 1;
      }).catch(err => {
        console.log(err);
      })
    },
    getAllTermTableList() {
      let req = {
        feederId: 0,
        termType: -1,
      };
      zdtzApi.getTermDataGridApi(req).then(res => {
        // let data = res.data;
        let data = res;
        if(data.length) {
          let tmp = [];
          data.forEach(item => {
            let obj = {};
            obj.id = item[0];
            obj.a = item[1];
            obj.b = deviceTypeObj[item[2]] ? deviceTypeObj[item[2]] : '';
            obj.c = comMethodObj[item[4]] ? comMethodObj[item[4]] : '';
            obj.d = channelConditionTypeObj[item[5]] ? channelConditionTypeObj[item[5]] : '';
            tmp.push(obj);
          })
          this.allTableList = tmp;
          // console.log(this.allTableList);
        } else {
          this.allTableList = [];
        }
      }).catch(err => {
        console.log(err);
      })
    },
    searchTerminal() {
      if(this.allTableList.length && this.terminalName) {
        this.tableList = this.allTableList.filter((item, index) => {
          if(item.a.indexOf(this.terminalName) > -1) {
            return true;
          } else {
            return false;
          }
        })
      }
      else {
        this.tableList = [];
        this.getTermTableList();
      }
    },
    setDeafultSubbarLabel() {
      this.areaList.length ? this.subbarList[0].activeItemValue = this.areaList[0].label : '';
      this.stationList.length ? this.subbarList[1].activeItemValue = this.stationList[0].label: '';
      this.feederList.length ?this.subbarList[2].activeItemValue = this.feederList[0].label: '';
      // console.log(this.subbarList);
    }
  },
  components: {
    SplitView,
    NoResult
  },
  created() {
    this.getAreaList().then(() => {
      this.areaList.shift();
      this.getSubstationByAreaId(this.areaList[0].id).then(() => {
        this.stationList.shift();
        if(!this.stationList.length) return;
        this.getFeederByAreaId(null, this.stationList[0].id).then(() => {
          this.feederList.shift();
          if(!this.feederList.length) return;
          this.feederId = this.feederList[0].id;
          this.getTermTableList();
        }).finally(() => {
          this.setDeafultSubbarLabel();
        });
      });
    })

    // this.getAreaList();
    // this.getSubstationByAreaId();
    // this.getFeederByAreaId();
    // this.getTermTableList();

    this.getAllTermTableList();
    // 终端类型
    this.getTermType();
  },
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#six{
  height: 100%;
  .nav-wp{
    height: 139.5px;
  }
  .search-wp{
    position: relative;
    overflow: hidden;
    margin: 15px 12.5px 0 12.5px;
    border-radius: 34px;
    border: 1px solid #dfdfdf;
    height: 34px;
    .placeholder-wp{
      position: absolute;
      pointer-events: none;
      z-index: 3;
      left: 50%;
      top: 50%;
      display: flex;
      justify-content: center;
      flex-direction: row;
      font-size: 14px;
      color: #b5b5b4;
      transform: translate(-50%, -50%);
       .search-icon{
        padding-right: 5px;
      }
    }
  }
  @extend %nav-with-one-title;
  @extend %sub-tabbar;
  @include subbar-popup(139.5px);
  @extend %pagination;
  .sub-tabbar{
    top: 97.5px;
  }
  .main-wp{
    height: calc(100% - 154.5px);
    .title-wp{
      display: flex;
      justify-content: space-around;
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      text-align: center;
      background-color: #33d8b6;
      color: #fff;
      .title-item{
        flex-basis: 25%;
      }
    }
    .sticky-wp{
      height: calc(100% - 40px);
      overflow: auto;
      background-color: #f3f3f3;
      .table-main{
        min-height: 100%;
        padding-bottom: 40px;
        .table-item{
          padding: 15px 0;
          line-height: 1.5;
          font-size: 0;
          background-color: #fff;
          @include border-1px(#dcdcdc);
          .item{
            display: inline-block;
            padding: 0 5px;
            width: 25%;
            vertical-align: middle;
            font-size: 14px;
            text-align: center;
          }
        }
      }
    }
    .pagination{
      margin-top: -40px;
    }
  }
  .van-cell{
    padding: 0;
    height: 34px;
    line-height: 34px;
    .van-hairline--top-bottom::after{
      content: none;
      border: 0;
      height: 0;
    }
    .van-cell__value::after{
      height: 0;
      content: none;
      border: 0;
    }
    .van-field__control {
      font-size: 14px;
      text-align: center;
      &:focus {
        color: #666;
      }
    }
    input:-ms-input-placeholder{
      color: #c7c7c7;
    }
    input::-webkit-input-placeholder{
      color: #c7c7c7;
    }
  }
  .van-cell-group::after{
    content: none;
  }
}
</style>


