import * as zdtzApi from '@/api/zdtaizhang';
import _util from '@/util/index';

export default {
  methods: {
    // 获取终端类型
    getTermType() {
      return new Promise((resolve, reject) => {
        zdtzApi.getTermTypesApi().then(res => {
          // let data = res.data;
          let data = res;
          if(Object.keys(data).length) {
            let tmp = [];
            for(let [key, value] of Object.entries(data)) {
              tmp.push({
                label: value,
                id: key
              })
            }
            tmp.unshift({label: '全部', id: 0});
            this.terminalTypeList = tmp;
            resolve();
          } else {
            this.$toast('获取的终端类型列表为空');
          }
        }).catch(err => {
          reject(err);
        })
      })
    },
  }
}


