import request from '@/util/request';
import jsonp from '@/util/jsonp.js';

import requestJquery from '@/util/requestJquery';

function joinObjAsQuery(obj) {
  return Object.values(obj).join('/');
}

// 缺陷列表查询
// HACK: api再次变化，先保持原先, 拆分成getDefectInfo和getDefectHisInfo
// export function getDefectInfo(data) {
//   let tmp = joinObjAsQuery(data);
//   return request({
//     url: 'terminalController/rest/mobile/getDefectInfo/' + tmp + '/0/0',
//     method: 'get',
//   })
// }
export function getDefectInfo(data) {
  let tmp = joinObjAsQuery(data);
  let url = '/terminalController/rest/mobile/getDefectInfo/' + tmp + '/0/0';
  return jsonp(url);
}

// 历史缺陷明细查询
// export function getDefectHisInfo(data) {
//   let tmp = joinObjAsQuery(data);
//   return request({
//     url: 'terminalController/rest/mobile/getDefectInfo/' + tmp,
//     method: 'get',
//   })
// }
export function getDefectHisInfo(data) {
  let tmp = joinObjAsQuery(data);
  let url = '/terminalController/rest/mobile/getDefectInfo/' + tmp;
  return jsonp(url);
}

// 缺陷确认接口
// export function confirmDefectInfo(data) {
//   return request({
//     url: 'terminalController/rest/mobile/confirmDefectInfo',
//     method: 'post',
//     data: data
//   })
// }
export function confirmDefectInfo(data) {
  let url = '/terminalController/rest/mobile/confirmDefectInfo';
  return jsonp(url, data);
}

