<template>
  <div id="seven">
    <div class="nav-wp">
      <van-nav-bar title="短消息订阅" @click-left="goBack" @click-right="addSubscribe" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="./zong-fanhui.png" alt="" class="goback-btn">
        </div>
        <div class="subscribe-btn-wp" slot="right">
          <img src="./xinzeng.png" alt="" class="subscribe-btn">
        </div>
      </van-nav-bar>
      <!-- sub navbar -- search -->
      <van-tabbar v-model="subbarActive" class="sub-tabbar">
        <van-tabbar-item v-for="(item, index) in subbarList" :key="index" @click="subbarItemClick(item, index)">
          <span>{{item.label}}</span>
          <div slot="icon" slot-scope="props" class="sub-tabbar-icon-wp">
            <img src="./xiala02.png" alt="" v-if="props.active">
            <img src="./xiala01.png" alt="" v-else>
          </div>
        </van-tabbar-item>
      </van-tabbar>
    </div>
    <split-view></split-view>
    <div class="empty-wp" v-if="noResult">
      <img src="./zanwu.png" alt="" class="empty-img">
      <p class="text">你还没有订阅信息哦</p>
      <p class="text">请点击右上角添加订阅信息</p>
    </div>
    <div class="main-wp" v-else>
      <!-- 按设备查看 -->
      <van-collapse v-model="activeName" accordion class="collapse" v-if="isDevTypeList">
        <van-collapse-item :name="index" class="collapse-item"  v-for="(item, index) in tableList" :key="index">
          <div slot="title" class="collapse-title">
            <span class="title">{{item.name}}</span>
            <span class="right-wp">
              <span class="num">{{item.warnLen}}</span>
            </span>
          </div>
          <div class="slot">
            <div class="item" v-for="(warnItem, warnItemIndex) in item.warnSubMod" :key="index+warnItemIndex">
              <span class="eq-name">{{warnItem.warnStatusLabel}}</span>
              <span class="delete-btn" @click="deleteWarnInDevice(item, warnItem)">删除</span>
            </div>
          </div>
        </van-collapse-item>
      </van-collapse>
      <!-- 按类型查看 -->
      <van-collapse v-model="activeName" accordion class="collapse" v-else>
        <van-collapse-item :name="index" class="collapse-item" v-for="(item, index) in tableList" :key="JSON.stringify(item)">
          <div slot="title" class="collapse-title">
            <span class="title">{{item.warnTypeLabel}} - {{item.warnStatusLabel}}</span>
            <span class="right-wp">
              <span class="num">{{item.devIds ? item.devIds.length : ''}}</span>
            </span>
          </div>
          <div class="slot">
            <div class="item" v-for="(devItem, devIndex) in item.devIds" :key="index+devIndex">
              <span class="eq-name">{{devItem[1]}}</span>
              <span class="delete-btn" @click="deleteWarnInWarn(item, devItem)">删除</span>
            </div>
          </div>
        </van-collapse-item>
      </van-collapse>
    </div>
    <!-- 条件筛选 popup -->
    <van-popup v-model="subbarPopupShow" class="filter-popup" overlay-class="filter-overlay">
      <span class="item" v-for="(item, index) in filterList" :key="index" @click="subbarPopupItemClick(item, index)" :class="{active: subbarPopupItemActive === index}">{{item.label}}</span>
    </van-popup>
  </div>  
</template>

<script>
// vuex
import {mapActions, mapMutations} from 'vuex';
// mixin
import apiMixin from './mixins/api-mixin';
// api
// 消息订阅api
import * as smsApi from '@/api/smsSuscribe';
// 组件
import SplitView from '@/components/split-view/index';
import NoResult from '@/components/no-result/index';
// const
import {deviceTypeList, errTypeList, userId} from '@/common/const/const';

const subbarList = [
  {label: '按设备查看', type: 0, activeItemIndex: 0, activeItemValue: ''},
  {label: '按类型查看', type: 1, activeItemIndex: -1, activeItemValue: ''},
];

function deleteReq() {
  return {
    userId: '',
    devId: '',
    warnType: '',
    warnStatus: '',
  }
}

export default {
  mixins: [apiMixin],
  computed: {
    noResult() {
      return this.tableList.length ? false : true;
    }
  },
  data() {
    return {
      isDevTypeList: true,

      userId,
      warnType: '',
      devType: '13500',

      tableList: [],

      subbarActiveIndex: 0,
      activeName: '1',

      subbarActive: 0,
      subbarList,
      subbarPopupItemActive: '',
      subbarPopupShow: false,
      filterList: deviceTypeList,
    }
  },
  methods: {
    ...mapMutations({
      setPushtimeType :'SET_PUSHTIME_TYPE',
      setEquipSelList: 'SET_EQUIP_SEL_LIST',
      setDeviceType: 'SET_DEVICE_TYPE'
    }),
    ...mapActions(['changeWarnType']),
    goBack(){
      // this.$router.replace({path: '/'});
      // android go to index.html;
      webCtrl.finish()
    },
    addSubscribe() {
      this.$router.push({ name: 'AddSubscribe'})
    },
    subbarItemClick(item, index) {
      this.subbarPopupItemActive = item.activeItemIndex;
      this.subbarPopupShow = true;
    },
    subbarPopupItemClick(item, index) {
      this.devType = item.id;
      if(this.subbarActive === 0) {
        this.subbarList[0].activeItemIndex = index;
        this.tableList = [];
        this.getDevList().then(() => {
          this.isDevTypeList = true;
        });
      } 
      else if(this.subbarActive === 1) {
        this.subbarList[1].activeItemIndex = index;
        this.tableList = [];
        this.getWarnList().then(() => {
          this.isDevTypeList = false;
        });
      }
      this.subbarPopupShow = false;
    },
    deleteWarnInDevice(item, warnItem) {
      console.log(item, warnItem, 'item');
      let req = deleteReq();
      req.userId = this.userId;
      req.warnType = warnItem.warnType;
      req.warnStatus = warnItem.warnStatus;
      req.devId = item.devId;
      console.log(req);
      this.deleteSubscribe(req);
    },
    deleteWarnInWarn(item, devItem) {
      console.log(item, devItem);
      let req = deleteReq();
      req.userId = this.userId;
      req.warnType = item.warnType;
      req.warnStatus = item.warnStatus;
      req.devId = devItem[0];
      this.deleteSubscribe(req);
    }
  },
  components: {
    SplitView,
    NoResult
  },
  beforeRouteEnter(to, from, next) {
    if(to.params) {
      next((vm) => {
        // vm.selectedList = to.params.selectedList;
        // console.log(to.params);
      })
    }
    next();
  },
  created() {
    this.getDevList();
    this.changeWarnType({});
    this.setPushtimeType({});
    this.setEquipSelList([]);
    this.setDeviceType({});
  }
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#seven{
  height: 100%;
  .nav-wp{
    height: 90px;
  }
  @extend %nav-with-one-title;
  @extend %sub-tabbar;
  @include subbar-popup(90px);
  
  .collapse-item{
    .delete-btn{
      text-align: center;
    }
  }
  .empty-wp{
    margin-top: 107px;
    text-align: center;
    .empty-img{
      margin-bottom: 30px;
      width: 67px;
      height: 80px;
    }
    .text{
      margin-bottom: 16px;
      font-size: 14px;
      color: #999;
    }
  }
  .main-wp{
    height: calc(100% - 105px);
    overflow-y: auto;
    .collapse{
      .collapse-title{
        display: flex;
        justify-content: space-between;
        .img{
          width: 17px;
          height: 9px;
        }
        .num{
          color: #f10100;
        }
      }
      .item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 22.5px 0 15px; 
        height: 50px;
        line-height: 50px;
        font-size: 14px;
        color: #656565;
        background: #f8f8f8;
        .delete-btn{
          width: 58px;
          height: 25px;
          line-height: 25px;
          font-size: 12px;
          color: #fff; 
          background-color: #e9aa44;
          border-radius: 3px;
        }
      }
      .van-cell{
        &::after{
          content: none;
        }
      }
      .van-collapse-item__content{
        padding: 0;
        &::after{
          content: none;
        }
      }
      .van-collapse-item__title {
        padding: 0;
        height: 50px;
        line-height: 50px;
        padding-left: 15px;
        color: #333;
      }

      .van-cell__right-icon{
        margin-left: 0;
        padding: 0 18px;
        line-height: 50px;
        font-weight: bold;
        font-size: 14px;
      }
      .van-cell__value{
        font-size: 15px;
      }
      .van-collapse-item{
        &::after{
          // content: none;
        }
        &.van-collapse-item--expanded{

        }
      }
      .collapse-item{
        // border-bottom: 1px solid #dfdfdf;
        // &.van-collapse-item--expanded{
        //   border-bottom: none; 
        // }
      }
    }
    .van-hairline--top-bottom{
      &::after{
        content: none;
        height: 0;
        width: 0;
      }
    }
  }
}
</style>


