import Vue from 'vue';
import Vuex from 'vuex';

import app from './modules/app';
import defect from './modules/defect';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    app,
    defect
  },
  getters: {
    pushTimeType: state => state.app.pushTimeType,
    
    warnTypeLabel: state => state.app.warnTypeLabel,
    warnType: state => state.app.warnType,
    warnStatus: state => state.app.warnStatus,

    equipSelList: state => state.app.equipSelList,
    tableList: state => state.app.tableList,

    deviceType: state => state.app.deviceType,
    
    _4confirmName: state => state.defect.confirmName,
    _4feederId: state => state.defect.feederId,
    _4device: state => state.defect.device,
    _4defectLevel: state => state.defect.defectLevel,
    _4defectType: state => state.defect.defectType,
    _4lastHandleTime: state => state.defect.lastHandleTime,
    _4defectDesc: state => state.defect.defectDesc,
  }
});



