// import request from '@/util/request';
import jsonp from '@/util/jsonp.js';

function joinObjAsQuery(obj) {
  return Object.values(obj).join('/');
}

// 加载终端详细参数；
// export function getTermInfoApi(data) {
//   let tmp = joinObjAsQuery(data);
//   return request({
//     url: 'terminalController/rest/termTz/termParaInfo/' + tmp,
//     method: 'get',
//   })
// }
export function getTermInfoApi(data) {
  let tmp = joinObjAsQuery(data);
  let url = '/terminalController/rest/termTz/termParaInfo/' + tmp;
  return jsonp(url);
}


