<template>
  <div id="add-subs">
    <van-nav-bar title="新增订阅" @click-left="goBack" class="nav-bar">
      <div class="goback-btn-wp" slot="left">
        <img src="../zong-fanhui.png" alt="" class="goback-btn">
      </div>
    </van-nav-bar>
    <split-view></split-view>
    <div class="main-wp">
      <div class="main-item" @click="pickErrType">
        <div class="con-wp">
          <span class="title">告警类型</span>
          <span class="text">{{warnTypeLabel}}</span>
        </div>
        <div class="right-arrow-wp">
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </div>
      </div>
      <!-- 设备类型 -->
      <div class="main-item" @click="pickDeviceType">
        <div class="con-wp">
          <span class="title">设备类型</span>
          <span class="text">{{deviceType.label}}</span>
        </div>
        <div class="right-arrow-wp">
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </div>
      </div>
      <div class="main-item" @click="pickEquip">
        <div class="con-wp">
          <span class="title">设备名称</span>
          <span class="text">{{equipName}}</span>
        </div>
        <div class="right-arrow-wp">
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </div>
      </div>
      <div class="main-item" @click="pickPushTime">
        <div class="con-wp">
          <span class="title">推送时段</span>
          <span class="text">{{pushTimeType.label}}</span>
        </div>
        <div class="right-arrow-wp">
          <img src="./xiangqing.png" alt="" class="right-arrow">
        </div>
      </div>
      <div class="submit-btn-wp" @click="submitBtn">
        <p class="text">新增</p>
      </div>
    </div>
    <!-- 选择 推送时段 -->
    <van-popup v-model="pushTimePickerShow" position="bottom">
      <van-picker :columns="pushTimeTypeList" value-key="label" @confirm="pushTimePickConfirm" @cancel="pushTimePickCancel" :show-toolbar="true"/>
    </van-popup>
    <!-- 选择 告警类型-->
    <van-popup v-model="errTypePickerShow" position="bottom">
      <van-picker :columns="errTypeList" value-key="label" @confirm="errTypePickConfirm" @cancel="errTypePickCancel" :show-toolbar="true" @change="errTypePickerChange"/>
    </van-popup>
    <!-- 选择 设备类型-->
    <van-popup v-model="deviceTypePickerShow" position="bottom">
      <van-picker :columns="deviceTypeList" value-key="label" @confirm="deviceTypePickConfirm" @cancel="deviceTypePickCancel" :show-toolbar="true"/>
    </van-popup>
  </div>
</template>

<script>
// vuex
import {mapGetters, mapMutations, mapActions} from 'vuex';
// api
import * as smsApi from '@/api/smsSuscribe';
// 组件
import SplitView from '@/components/split-view/index';
// mixin
import apiMixin from '../mixins/api-mixin';
// const
import {deviceTypeList, errTypeList, userId} from '@/common/const/const';

const pushTimeTypeList = [
  {
    label: '及时推送',
    id: 1,
  },
  {
    label: '工作日推送',
    id: 2,
  }
];

function warnTypeReq() {
  return {
    userId,
    "warnSub": {
      "warnType": '',
      "warnStatus": [],
      "devIds": []
    },
    "pushType": '',
    "devType": '',
  }
}

function deviceReq() {
  return  {
    userId,
    "devType": "",
    "devId": []
  }
}

export default {
  mixins: [apiMixin],
  data() {
    return {
      pushTimePickerShow: false,
      pushTimeTypeList,

      errTypePickerShow: false,
      errTypeList: [
        {
          values: [],
          className: 'column1'
        },
        {
          values: [],
          className: 'column2',
        }
      ],
      
      deviceTypePickerShow: false,
      deviceTypeList,
    }
  },
  computed: {
    ...mapGetters(['warnTypeLabel', 'warnType', 'warnStatus' ,'pushTimeType', 'equipSelList', 'deviceType']),
    equipName() {
      let nameList = [];
      this.equipSelList.forEach((item) => {
        nameList.push(item.name);
      })
      return nameList.join(',')
    }
  },
  created() {
    let columns;
    let obj1 = {};
    let obj2 = {};
    errTypeList.forEach((item, index) => {
      this.errTypeList[0].values.push(item);
    })
    this.errTypeList[1].values = errTypeList[0].sub;
  },
  methods: {
    ...mapMutations({
      setPushtimeType :'SET_PUSHTIME_TYPE',
      setEquipSelList: 'SET_EQUIP_SEL_LIST',
      setDeviceType: 'SET_DEVICE_TYPE'
    }),
    ...mapActions(['changeWarnType']),
    goBack(){
      this.emptySelect();
      this.$router.replace({name: 'seven'});
    },
    // 选择 推送时段
    pickPushTime() {
      this.pushTimePickerShow = true;
    },
    pushTimePickConfirm(item, index) {
      // console.log(item);
      this.setPushtimeType(item);
      this.pushTimePickerShow = false;
    },
    pushTimePickCancel() {
      this.pushTimePickerShow = false;
    },

    // 选择告警类型
    pickErrType() {
      this.errTypePickerShow = true;
    },
    errTypePickConfirm(itemList, indexList) {
      let warnType = itemList[0].id;
      let warnStatus = [itemList[1].id];
      let warnTypeLabel = itemList[1].label;

      this.changeWarnType({warnType, warnStatus, warnTypeLabel});
      this.errTypePickerShow = false;
    },
    errTypePickCancel() {
      this.errTypePickerShow = false;
    },

    // 选择设备类型
    pickDeviceType() {
      this.deviceTypePickerShow = true;
    },
    deviceTypePickConfirm(item, index) {
      this.setDeviceType(item);
      this.setEquipSelList([]);
      this.deviceTypePickerShow = false;
    },
    deviceTypePickCancel() {
      this.deviceTypePickerShow = false;
    },

    // 告警类型联动
    errTypePickerChange(picker, values, columnIndex){
      let column1Label = values[0].label;
      errTypeList.forEach((item, index) => {
        if(item.label === column1Label) {
          picker.setColumnValues(1, item.sub);
        }
      })
    },
    pickEquip() {
      if(this.deviceType.id) {
        this.$router.push({name: 'equipmentSelect'});
      } else {
        this.$toast('请先选择设备类型');
      }
    },
    // 新增 submit btn
    submitBtn() {
      // let deviceReq = this.getdeviceReq();
      let warnTypeReq = this.getWarnTypeReq();
      // console.log(deviceReq);
      console.log(warnTypeReq);
      // debugger;
      // let promise1 = this.addSubscribeByDevice(deviceReq);
      // let promise2 = this.addSubscribe(warnTypeReq);
      
      // Promise.all([promise1, promise2]).then(() => {
      //   this.$toast('新增订阅成功');
      //   this.emptySelect();
      //   this.$router.push({name: 'seven'});
      // }).catch(err => {
      //   this.$toast('新增订阅失败')
      // });
      this.addSubscribe(warnTypeReq).then(() => {
        this.$toast('新增订阅成功');
        this.emptySelect();
        this.$router.push({name: 'seven'});
      }).catch(err => {
        this.$toast('新增订阅失败')
      });
    },
    getdeviceReq() {
      let req = deviceReq();
      let deviceIdList = [];
      this.equipSelList.forEach(item => {
        deviceIdList.push(item.value);
      })
      req.devId = deviceIdList;
      req.devType = this.deviceType.id;
      return req;
    },
    getWarnTypeReq() {
      let req = warnTypeReq();
      req.warnSub.warnType = this.warnType;
      req.warnSub.warnStatus = this.warnStatus;
      req.warnSub.devIds = this.getdeviceReq().devId;
      req.pushType = this.pushTimeType.id;
      req.devType = this.deviceType.id;
      return req;
    },
    emptySelect() {
      this.changeWarnType({});
      this.setPushtimeType({});
      this.setEquipSelList([]);
      this.setDeviceType({});
    }
  },
  components: {
    SplitView
  },
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";
#add-subs{
  @extend %nav-with-one-title;
  .main-wp{
    padding: 0 15px;
    .main-item{
      position: relative;
      height: 59px;
      line-height: 59px;
      font-size: 14px;
      color: #666;
      @include border-1px(#e2e2e2);
      .con-wp{
        padding: 0 5px;
        text-align: left;
        .title{
          display: inline-block;
          vertical-align: top;
          line-height: 59px;
          width: 90px;
        }
        .text{
          display: inline-block;
          margin-left: 15px;
          max-width: 50%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
      .right-arrow-wp{
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        align-items: center;
        margin-right: -15px;
        padding: 0 20px 0 20px;
        height: 100%;
        .right-arrow{
          width: 9px;
          height: 18.5px;
        }
      }
    }
    .submit-btn-wp{
      margin-top: 110px;
      height: 45px;
      line-height: 45px;
      border-radius: 45px;
      font-size: 17.5px;
      text-align: center;
      background: #1bbc9b;
      color: #fff; 
    }
  }
}
</style>



