import * as equipErrApi from '@/api/equip_err';
import * as commonApi from '@/api/common';
import {userId} from '@/common/const/const';

function trListTransfer(obj) {
  let result = [];
  for(let [key, value] of Object.entries(obj)) {
    let resultItem = {};
    resultItem.name = key.toUpperCase();
    resultItem.items = [];
    if(!value.length) continue;
    value.forEach(item => {
      resultItem.items.push({
        name: item[1],
        value: item[0]
      })
    })
    result.push(resultItem);
  }
  return sortTrList(result);
}

function sortTrList(arr) {
  let reg = /[A-Z]/;
  let result = 0;
  return arr.sort((a, b) => {
    if(a.name === '#') {
      return 1;
    } else if(b.name === '#') {
      return -1
    } else if(a.name < b.name) {
      return -1;
    } else {
      return 1;
    }
  })
}

// TODO: 放入vuex中管理；
export default {
  methods: {
    // 获取区域列表
    // 不需要参数
    // 需要提供data：areaList
    getAreaList() {
      return new Promise((resolve, reject) => {
        let req = {
          userId,
        }
        equipErrApi.getAreaList(req).then(res => {
          // let data = res.data;
          let data = res;
          if(Object.keys(data).length) {
            let tmp = [];
            for(let [key, value] of Object.entries(data)) {
              tmp.push({
                label: value,
                id: key
              })
            }
            tmp.unshift({label: '全部', id: 0});
            this.areaList = tmp;
            resolve();
          } else {
            console.log('获取的区域列表为空');
          }
        }).catch(err => {
          // console.log('xxxx')
          reject(err);
        })
      })
    },
    // 获取线路列表 
    // 需要提供参数 areaId，没有即为全部
    // 需要提供data: feederList
    // TODO: 改成传{areaId, stationId}
    getFeederByAreaId(areaId, stationId) {
      this.feederList = [];
      return new Promise((resolve, reject) => {
        let req = {};
        if(!areaId && !stationId) {
          req = null;
        }
        else if(stationId) {
          req.stationId = stationId;
        }
        else if(areaId) {
          req.areaId = areaId;
        }
        // console.log(req);
        equipErrApi.getFeederByAreaId(req).then(res => {
          let data = res;
          // let data = res.data;
          if(Object.keys(data).length) {
            let tmp = [];
            for(let [key, value] of Object.entries(data)) {
              tmp.push({
                label: value,
                id: key,
              })
            }
            tmp.unshift({
              label: '全部',
              id: 0,
            })
            this.feederList = tmp;
            resolve(res);
          } else {
            resolve();
          }
        }).catch(err => {
          console.log(err);
          reject(err);
        })
      })
    },
    // 获取变电站列表
    // post 参数{areaId}，没有即为全部
    // 需要提供data: stationList
    getSubstationByAreaId(areaId) {
      return new Promise((resolve, reject) => {
        let req = {};
        if(areaId) {
          req.areaId = areaId;
        } else {
          req = null;
        }
        equipErrApi.getSubstationByAreaId(req).then(res => {
          let data = res;
          // let data = res.data;
          if(Object.keys(data).length) {
            let tmp = [];
            for(let [key, value] of Object.entries(data)) {
              tmp.push({
                label: value,
                id: key,
              })
            }
            tmp.unshift({
              label: '全部',
              id: 0,
            })
            this.stationList = tmp;
          }
          resolve(res);
        }).catch(err => {
          console.log(err);
          reject(err);
        })
      })
    },
    // 获取终端设备列表
    getTrListByFeederId(feederId) {
      return new Promise((resolve, reject) => {
        let req = {
          feederId: feederId || 0
        };
        commonApi.getTrListByFeederId(req).then(res => {
          // let data = res.data;
          let data = res;
          this.equipmentData = trListTransfer(data)
          if(!this.equipmentData.length) {
            // this.$toast('无终端设备数据')
          }
          resolve(res);
        }).catch(err => {
          console.log(err);
          reject(err);
        })
      })
    },

    // 消息订阅获取终端设备， 参数: stId;
    getDeviceListByStId(stId) {
      return new Promise((resolve, reject) => {
        let req = {
          stId: stId || 0
        };
        // console.log(req);
        // debugger;
        commonApi.getDeviceListByStId(req).then(res => {
          // let data = res.data;
          let data = res;
          this.equipmentData = trListTransfer(data)
          if(!this.equipmentData.length) {
            this.$toast('无终端设备数据')
          }
          this.equipmentDataAddSelected();
          resolve(res);
        }).catch(err => {
          console.log(err);
          reject(err);
        })
      })
    },
    // 消息订阅获取终端设备， 参数: feederId;
    getDeviceListByFeederId(feederId) {
      return new Promise((resolve, reject) => {
        let req = {
          feederId: feederId || 0
        };
        console.log(req);
        // debugger;
        commonApi.getDeviceListByFeederId(req).then(res => {
          let data = res;
          // let data = res.data;
          this.equipmentData = trListTransfer(data)
          if(!this.equipmentData.length) {
            this.$toast('无终端设备数据')
          }
          this.equipmentDataAddSelected();
          resolve(res);
        }).catch(err => {
          console.log(err);
          reject(err);
        })
      })
    }
  }
}


