import * as defectApi from '@/api/defect';
import _util from '@/util/index';

export default {
  methods: {
    // HACK: api中新增参数，所以为保持原有功能，将其拆分成两个独立接口分别请求；
    // 获取缺陷列表
    getDefectInfo() {
      let req = {
        feederId: this.feederId,
        defectType: this.defectType,
        ifConfirm: this.ifConfirm,
      };
      this.tableList = [];
      return new Promise((resolve, reject) => {
        defectApi.getDefectInfo(req).then(res => {
          let data = res;
          console.log(res);
          // let data = res.data.resultValue.items;
          if(data.length) {
            this.tableList = data;
          } else {
            // this.$toast('暂时没有缺陷');
          }
          resolve(res);
        }).catch(err => {
          reject(err);
        })
      })
    },
    getAllDefectInfo() {
      let req = {
        feederId: 0,
        defectType: 0,
        ifConfirm: 0,
      };
      this.allTableList = [];
      return new Promise((resolve, reject) => {
        defectApi.getDefectInfo(req).then(res => {
          let data = res;
          if(data.length) {
            this.allTableList = data;
          } else {
            // this.$toast('暂时没有缺陷');
          }
          resolve(res);
        }).catch(err => {
          reject(err);
        })
      })
    },
    // 获取历史缺陷列表
    getDefectHisInfo() {
      let req = {
        feederId: this.feederId,
        defectType: this.defectType,
        ifConfirm: this.ifConfirm,
        fromTime: _util.dashDateWithoutSpace(this.fromTime),
        endTime: _util.dashDateWithoutSpace(this.endTime),
      };
      return new Promise((resolve, reject) => {
        defectApi.getDefectHisInfo(req).then(res => {
          let data = res;
          // let data = res.data.resultValue.items;
          if(data.length) {
            this.tableList = data;
          } else {
            // this.$toast('暂时没有缺陷');
          }
          resolve(res);
        }).catch(err => {
          reject(err);
        })
      })
    },
  }
}


