import jsonp from '@/util/jsonp';
import jsonpJSON from '@/util/jsonpJSON';

function joinObjAsQuery(obj) {
  return Object.values(obj).join('/');
}

// 按设备查询
// export function getDevList(data) {
//   let tmp = joinObjAsQuery(data);
//   return request({
//     url: '/terminalController/rest/mobile/getDevList/' + tmp,
//     method: 'get',
//   })
// }
export function getDevList(data) {
  let tmp = joinObjAsQuery(data);
  let url = '/terminalController/rest/mobile/getDevList/' + tmp;
  return jsonp(url);
}


