<template>
  <div id="confirm-defect">
    <div class="nav-wp">
      <van-nav-bar title="缺陷确认" @click-left="goBack" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="../../zong-fanhui.png" alt="" class="goback-btn">
        </div>
      </van-nav-bar>
    </div>
    <split-view></split-view>
    <div class="main-wp">
      <div class="defect-info-item">
        <div class="title-wp">
          <span class="title">{{errTypeObj[item.defectType]}}</span>
          <span class="submit-time" v-if="item.pushTime">提交时间：{{item.pushTime}}</span>
        </div>
        <div class="info-wp">
          <div class="info-list">
            <div class="info-item">
              <span class="title">缺陷标识</span>
              <span class="text">{{item.defectId}}</span>
            </div>
            <div class="info-item">
              <span class="title">设备名称</span>
              <span class="text">{{item.devName}}</span>
            </div>
            <div class="info-item">
              <span class="title">所属线路</span>
              <span class="text">{{item.feederName}}</span>
            </div>
          </div>
        </div>
      </div>
      <split-view></split-view>
      <div class="confirm-form">
        <div class="confirm-form-item" @click="pickDefectLevel">
          <div class="con-wp">
            <span class="title">缺陷等级</span>
            <span class="text">{{defectLevelValue}}</span>
          </div>
          <div class="right-arrow-wp">
            <img src="../../xiangqing.png" alt="" class="right-arrow">
          </div>
        </div>
        <div class="confirm-form-item" @click="pickDeadline">
          <div class="con-wp">
            <span class="title">最迟消缺时间</span>
            <span class="text">{{deadline | dashDateTime}}</span>
          </div>
          <div class="right-arrow-wp">
            <img src="../../xiangqing.png" alt="" class="right-arrow">
          </div>
        </div>
        <div class="defect-desc-wp">
          <div class="input-label-wp">
            <span class="title">缺陷描述</span>
          </div>
          <van-cell-group>
            <van-field v-model="defectDesc" type="textarea" placeholder="请输入缺陷描述" rows="2" autosize>
            </van-field>
          </van-cell-group>
        </div>
        <div class="submit-btn-wp" @click="confirmDefectInfo">确认提交</div>
      </div>
    </div>
    <!-- 时间选择器 -->
    <van-popup v-model="timePickerShow" position="bottom" class="timepicker-popup">
      <van-datetime-picker v-model="timePicker.date" type="datetime" :min-date="timePicker.minDate" @confirm="pickTimeConfirm" @cancel="pickTimeCancel"/>
    </van-popup>
    <!-- 缺陷等级选择 -->
    <van-popup v-model="defectLevelPickerShow" position="bottom" class="">
      <van-picker :columns="defectLevelList" value-key="name" @confirm="defectLevelPickConfirm" @cancel="defectLevelPickCancel" :show-toolbar="true"/>
    </van-popup>
  </div>
</template>

<script>
import SplitView from '@/components/split-view/index';
import {dashDateTime} from '@/filters';
import _util from '@/util/index';
import * as defectApi from '@/api/defect';

const errTypeObj = {
  '5': '终端长期离线',
  '6': '终端频繁投退',
  '7': '开关遥控失败',
  '8': '信号抖动',
  '9': '数据缺漏异常',
  '10': '电源故障',
  '11': '蓄电池故障',
  '12': '流量越限',
  '99': '其他',
};

const defectLevelList = [
  {
    name: '一般',
    value: 1
  },
  {
    name: '紧急',
    value: 2
  }
]

const MIN_DATE = new Date(2018, 1, 1);
const MAX_DATE = new Date();

export default {
  data() {
    return {
      item: {},
      
      errTypeObj,
      timePickerShow: false,
      timePicker: {
        minDate: MIN_DATE,
        maxDate: MAX_DATE,
      },

      defectLevelList,
      defectLevelPickerShow: false,

      defectLevel: '',
      defectLevelValue: '',
      deadline: '',
      defectDesc: '',
    }
  },
  methods: {
    goBack(){
      this.$router.replace({path: '/four'});
    },

    // 最迟消缺时间选取相关
    pickDeadline() {
      this.timePickerShow = true;
    },
    pickTimeConfirm(value) {
      this.deadline = value;
      this.timePickerShow = false;
    },
    pickTimeCancel() {
      this.timePickerShow = false;
    },

    // 缺陷等级选取相关
    pickDefectLevel(){
      this.defectLevelPickerShow = true;
    },
    defectLevelPickConfirm(item) {
      this.defectLevel = item.value;
      this.defectLevelValue = item.name,
      this.defectLevelPickerShow = false;
    },
    defectLevelPickCancel() {
      this.defectLevelPickerShow = false;
    },

    // 缺陷确认
    confirmDefectInfo(){
      let req = {
        defectId: this.item.defectId,
        defectLevel: this.defectLevel,
        lastHandleTime: _util.dashDateTimeWithoutSpace(this.deadline),
        describe: this.defectDesc,
      }
      defectApi.confirmDefectInfo(req).then(res => {
        // TODO:
        // let data = res.data;
        let data = res;
        if(data === 1) {
          this.$toast('缺陷确认操作成功');
          this.$router.replace({name: 'four'});
        } else {
          this.$toast('缺陷确认操作失败');
        }
      }).catch(err => {
        console.log(req);
      })
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.item = to.params;
      console.log(to.params);
    })
  },
  components: {
    SplitView
  },
  filters: {
    dashDateTime
  },
  deactivated() {
    this.$destroy();
  }
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#confirm-defect{
  @extend %nav-with-one-title;
  @extend %submit-btn;
  .nav-wp{
    height: 48px;
  }
  .main-wp{
    @extend %defect-info-item;
    .confirm-form{
      padding: 0 15px;
    }
    .confirm-form-item{
      position: relative;
      height: 59px;
      line-height: 59px;
      font-size: 14px;
      color: #666;
      @include border-1px(#e2e2e2);
      .con-wp{
        height: 100%;
        padding: 0 5px;
        text-align: left;
        .title{
          display: inline-block;
          vertical-align: top;
          line-height: 59px;
          width: 90px;
        }
        .text{
          display: inline-block;
          margin-left: 15px;
          max-width: 52%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
      .right-arrow-wp{
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        align-items: center;
        margin-right: -15px;
        padding: 0 20px 0 20px;
        height: 100%;
        .right-arrow{
          width: 9px;
          height: 18.5px;
        }
      }
    }
    .defect-desc-wp {
      .input-label-wp{
        padding-left: 5px;
        height: 59px;
        line-height: 59px;
        font-size: 14px;
        color: #666;
      }
      .van-cell-group{
        &::after{
          content: none;
        }
      }
      .van-cell{
        border: 1px solid #d7d7d7;
        border-radius: 3px;
      }  
    }
  }
  .timepicker-popup {
    width: 100%;
  }
}
</style>


