<template>
  <div class="map">
    <div class="nav-wp">
      <van-nav-bar title="地图定位" @click-left="goBack" @click-right="goLineMap" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="../../zong-fanhui.png" alt="" class="goback-btn">
        </div>
        <div class="go-line-wp" slot="right">
          <img src="../../zong-fanhui.png" alt="" class="goline-btn">
        </div>
      </van-nav-bar>
    </div>
    <div id="map-wp"></div>
    <div class="info-layer" v-show="failureInfo">
      <div class="title">故障信息</div>
      <p class="desc">
        {{failureInfo.qypd}}
      </p>
      <table class="detail-table">
        <tr>
          <th>故障线路</th>
          <th>故障类型</th>
          <th>故障开始时间</th>
        </tr>
        <tr>
          <td>{{failureInfo.gzxl}}</td>
          <td>{{failureInfo.faultTypeLabel}}</td>
          <td>{{failureInfo.gzsj}}</td>
        </tr>
      </table>
    </div>
  </div>  
</template>

<script>
import _util from "@/util/index";

import * as fdApi from "@/api/fault_determine";

import mapMixin from '../../mixins/map-mixin';

import {gisServerAddr} from '@/common/const/common';

import iconImg from "./icon.png";

// const 故障类型
import {errTypeObj} from '@/common/const/gzyp';

import proj from 'ol/proj';
import View from 'ol/view';
import Map from 'ol/map';
import GeoJSON from "ol/format/geojson";
import Vector from "ol/source/vector";
import Vector2 from "ol/layer/vector";
import Style from "ol/style/style";
import Icon from "ol/style/icon";
import Overlay from "ol/overlay";

var geojsonOrigin = {
  type: "FeatureCollection",
  crs: {
    type: "name"
  },
  features: [
    {
      type: "Feature",
      geometry: {
        type: "Point",
        coordinates: [118.780612, 32.072743]
      }
    }
  ]
};

export default {
  mixins: [mapMixin],
  data() {
    return {
      failureInfo: "",
      infoLayerShow: true,

      req: "",

      map: {},
      geojsonOrigin,
      // 未经format的坐标
      originCoordinate: [119.9721, 31.7976],

      mapOption: {
        center: [119.9721, 31.7976],
        zoom: 10,
        target: 'map-wp'
      },
      gisServerAddr,
    };
  },
  computed: {
    // format之后的overlay的位置，
    coordinateClone() {
      return proj.fromLonLat(this.originCoordinate);
    },
    geojson() {
      let clone = _util.deepClone(this.geojsonOrigin);
      clone.features[0].geometry.coordinates = this.originCoordinate;
      return clone;
    }
  },
  watch: {
    failureInfo() {
      this.setFaultTypeInfo();
    }
    // failureInfo() {
    //   let infoLayerCt = document.querySelector(".info-layer");
    //   let overlay = new Overlay({
    //     element: infoLayerCt,
    //     offset: [0, -20],
    //     positioning: "bottom-center",
    //     // 触发setPosition时，保证overlay可见;
    //     autoPan: true,
    //     autoPanAnimation: {
    //       duration: 250
    //     }
    //   });
    //   overlay.setPosition(this.coordinateClone);
    //   this.map.addEventListener("singleclick", () => {
    //     // HACK: 使用v-show有一点问题
    //     this.infoLayerShow = !this.infoLayerShow;
    //     if (this.infoLayerShow) {
    //       overlay.setPosition(this.coordinateClone);
    //     } else {
    //       overlay.setPosition(undefined);
    //     }
    //     return false;
    //   });
    //   this.map.addOverlay(overlay);
    // }
  },
  methods: {
    goBack() {
      this.$router.push("/one");
    },
    goLineMap() {
      this.$router.replace({name: 'lineMap', params: {dxt: this.failureInfo.dxt}});
    },
    createPowerMap(options = {}) {
      let center = options.center ? options.center : [110, 32];
      let zoom = options.zoom ? options.zoom : 6;
      let centerPrj_ = options.centerPrj ? options.centerPrj : 'EPSG:4326';

      let target = options.target;

      let view = new View({
        center: proj.transform(center, centerPrj_, 'EPSG:3857'),
        zoom,
      });
      let map = new Map({
        target,
        view,
        layers: [],
        renderer: 'canvas',
      });
      return map;
    },
    initDeviceLayer(geojson) {
      var json = new GeoJSON();
      var all_features = json.readFeatures(geojson, {
        dataProjection: "EPSG:4326",
        featureProjection: "EPSG:3857"
      });
      var origin_center = all_features[0].getGeometry().getExtent();
      var view = new View({
        center: origin_center,
        zoom: 15,
        minZoom: 10,
        maxZoom: 17
      });
      this.map.setView(view);
      var vectorSource = new Vector();
      vectorSource.addFeatures(all_features);

      return new Vector2({
        source: vectorSource,
        style: new Style({
          image: new Icon({
            scale: 0.5,
            src: iconImg
          })
        })
      });
    },
    addInfoLayer() {
      let infoLayerCt = document.querySelector(".info-layer");
      let overlay = new Overlay({
        element: infoLayerCt,
        offset: [0, -20],
        positioning: "bottom-center",
        // 触发setPosition时，保证overlay可见;
        autoPan: true,
        autoPanAnimation: {
          duration: 250
        }
      });
      overlay.setPosition(this.coordinateClone);
      this.map.addEventListener("singleclick", () => {
        // HACK: 使用v-show有一点问题
        this.infoLayerShow = !this.infoLayerShow;
        if (this.infoLayerShow) {
          overlay.setPosition(this.coordinateClone);
        } else {
          overlay.setPosition(undefined);
        }
        return false;
      });
      this.map.addOverlay(overlay);
    },
    // 与故障研判首页 indicators规则关联；
    setFaultTypeInfo() {
      console.log(this.failureInfo.fault_type);
      if(this.failureInfo && this.failureInfo.fault_type != null) {
        if (this.failureInfo.fault_type === 2 || 
          this.failureInfo.fault_type === 4 || 
          this.failureInfo.fault_type % 10 === 2 || 
          this.failureInfo.fault_type % 10 === 4) {
          this.$set(this.failureInfo, 'faultTypeLabel', errTypeObj[1]) 
        } 
        else {
          this.$set(this.failureInfo, 'faultTypeLabel', errTypeObj[2]) 
        }
      }
    },
  },
  mounted() {
    this.map = this.createPowerMap(this.mapOption);
    this.addWMTSLayer(`http://${this.gisServerAddr}/geowebcache/service/wmts`, 'changzhou', this.map);

    // layer2
    let device_layer = this.initDeviceLayer(this.geojson);
    this.map.addLayer(device_layer);
    this.addInfoLayer();
  },
  created() {
    this.$nextTick(() => {
      this.setFaultTypeInfo();
    })
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log(to.params);
      vm.failureInfo = to.params;
      // vm.req = to.params;
      // 暂时用不到
      // fdApi.getDagzDetailInfo(to.params).then(res => {
      //   console.log(res);
      // }).catch(err => {
      //   console.log(err);
      // })
      // console.log(to.params);
    });
  },
  // 不需要保留页面状态
  // deactivated() {
  //   this.$destroy();
  // }
};
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

.map {
  height: calc(100% - 48px);
  @extend %nav-with-one-title;
  #map-wp {
    height: 100%;
    width: 100%;
  }
  .info-layer {
    z-index: 100;
    padding: 16px 13.5px;
    width: 260px;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    border-radius: 3px;
    .title {
      font-size: 15px;
    }
    .desc {
      margin-top: 12px;
      font-size: 11px;
      line-height: 1.5;
    }
    .detail-table {
      margin-top: 24px;
      width: 100%;
      border: 1px solid #fff;
      text-align: center;
      th,
      td {
        height: 35px;
        line-height: 1.5;
        padding: 5px;
        border: 1px solid #fff;
        vertical-align: middle;
      }
      th:nth-child(-n + 2) {
        width: 70px;
      }
      th {
        font-size: 12px;
      }
      td {
        font-size: 11px;
      }
    }
  }
}
</style>



