          //   }
          // }).catch(err => {
          //   this.$toast('获取环境异常数据失败');
          // })
        }
      }
    },
    // type 0 1 2 默认为0，1为历史查询
    resizeMainH(type) {
      let documentH = document.documentElement.clientHeight || document.body.clientHeight;
      let mainH;
      let bottomTabbarH = 47.5;
      let navH;
      if(!type || type === 0) {
        navH = 105;
        mainH = documentH - navH - bottomTabbarH + 'px';
      } 
      else if(type === 1) {
        navH = 135;
      }
      mainH = documentH - navH - bottomTabbarH + 'px';
      if(!this.$refs['mainWp']) return;
      this.$refs['mainWp'].style.height = mainH;
    },
    setAreaLabel(label, index) {
      this.subbarList[0][0].activeItemIndex = this.subbarList[1][0].activeItemIndex = this.subbarList[2][0].activeItemIndex = index;
      this.subbarList[0][0].activeItemValue = this.subbarList[1][0].activeItemValue = this.subbarList[2][0].activeItemValue = label;
    },
    setFeederLabel(label, index) {
      this.subbarList[0][1].activeItemIndex = this.subbarList[2][1].activeItemIndex = index;
      this.subbarList[0][1].activeItemValue = this.subbarList[2][1].activeItemValue = label;
    },
    setStationLabel(label, index) {
      this.subbarList[1][1].activeItemIndex = index;
      this.subbarList[1][1].activeItemValue = label;
    },
    setTrErrLabel(label, index) {
      this.subbarList[0][2].activeItemIndex = index;
      this.subbarList[0][2].activeItemValue = label;
    },
    setFeederErrLabel(label, index) {
      this.subbarList[1][2].activeItemIndex = index;
      this.subbarList[1][2].activeItemValue = label;
    },
    setEnvErrLabel(label, index) {
      this.subbarList[2][2].activeItemIndex = index;
      this.subbarList[2][2].activeItemValue = label;
    },
  },
  watch: {
    tabbarActive(value) {
      // 历史查询
      this.setTableList([]);
      if(value === 0) {
        this.navTitleList = navTitleList;
        this.resizeMainH();
      }
      else if(value === 1) {
        this.navTitleList = navHisTitleList;
        this.resizeMainH(1);
      }
    },
    noResult(value) {
      if(!value) {
        if(this.tabbarActive === 0) {
          this.resizeMainH();
        } 
        else if(this.tabbarActive === 1) {
          this.resizeMainH(1);
        }
      }
    }
  },
  created() {
    this.resizeMainH();

    this.getAreaList().then(() => {
      this.areaList.shift();
      if(!this.areaList.length) return;
      this.subbarList[0][0].activeItemIndex = this.subbarList[1][0].activeItemIndex = this.subbarList[2][0].activeItemIndex = 0;
      this.subbarList[0][0].activeItemValue = this.subbarList[1][0].activeItemValue = this.subbarList[2][0].activeItemValue = this.areaList[0].label;

      this.getFeederByAreaId(this.areaList[0].id).then(() => {
        this.feederList.shift();
        if(!this.feederList.length) return;

        this.subbarList[0][1].activeItemIndex = this.subbarList[2][1].activeItemIndex = 0;
        this.subbarList[0][1].activeItemValue = this.subbarList[2][1].activeItemValue = this.feederList[0].label;

        this.feederId = this.feederList[0].id;
        this.getTodayTrWarn().then(() => {
          if(!this.tableList.length) {
            // this.noResult = true;
          }
        });
      });
      this.getSubstationByAreaId(this.areaList[0].id).then(() => {
        this.stationList.shift();
        if(!this.stationList.length) return;

        this.subbarList[1][1].activeItemIndex = 0;
        this.subbarList[1][1].activeItemValue = this.stationList[0].label;

        this.substationId = this.stationList[0].id
      })

    });
  },
  components: {
    SplitView,
    NoResult
  },
  filters: {
    dashDate
  }
}
</script>

<style lang="scss">
@import "@/common/scss/variable.scss";
@import "@/common/scss/index.scss";

#three{
  position: relative;
  height: 100%;
  @extend %nav-with-one-title;
  .nav-wp{
    height: 90px;
  }
  .nav-bar{
    .nav-title{
      line-height: 48px;
      display: flex;
      justify-content: center;
      align-items: center;
      .dropdown-btn{
        margin-left: 4px;
        width: 9px;
        height: 5px;
      }
    }
  }
  .nav-popup {
    top: 92px;
    display: flex;
    justify-content: space-between;
    padding: 26px 9px;
    width: 100%;
    background-color: #f6f6f6;
    .item{
      padding: 0 9.5px;
      line-height: 35px;
      font-size: 14px;
      border: 1px solid #dcdcdc;
      border-radius: 3px;
      &.active{
        color: #1bbc9b;
      }
    }
  }
  @include subbar-popup(90px);
  .bottom-tabbar{
    height: 47.5px;
    .van-tabbar-item__icon{
      margin-bottom: 2px;
      width: 21px;
      height: 20px;
    }
    .van-tabbar-item__text{
      span{
        font-size: 9.5px;
        color: #333;
      }
    }
    .split{
      margin-top: 13.5px;
      height: 20px;
      width: 1px;
      background: #999;
    }
  }
  .sub-tabbar{
    position: absolute;
    top: 47.5px;
    height: 42px;
    &::after{
      border-top-width: 0; 
      border-bottom-color: #f3f3f3; 
    }
    .van-tabbar-item{
      display: flex;
      align-items: center;
      flex-direction: row;
      .van-tabbar-item__text{
        order: 1;
        font-size: 14px;
        color: #333;
      }
      .van-tabbar-item__icon{
        order: 2;
      }
    }
    .sub-tabbar-icon-wp{
      img{
        margin-left: 6px;
        vertical-align: middle;
        width: 6.5px;
        height: 4px;
      }
    }
  }
  .table-wp{
    height: calc(100% - 154.5px);
    .title-wp{
      display: flex;
      justify-content: space-between;
      padding: 0;
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      color: #fff;
      background-color:#33d8b6;
      span{
        flex-basis: 33.33%;
        text-align: center;
      }
    }
    .sticky-wp{
      overflow-y: auto;
      height: calc(100% - 40px);
      .table-main{
        min-height: 100%;
        padding-bottom: 40px;
        .van-list{
          width: 100%;
          .van-cell{
            padding: 12px 0;
            .van-cell__value div{
              display: flex;
              justify-content: space-between;
              align-items: center;
              padding: 0;
              span{
                text-align: center;
                flex-basis: 33.33%;
              }
            }
            &::after{
              left: 0;
              border-color: #dfdfdf; 
            }
          }
        }
      }
    }
    .pagination{
      margin-top: -40px;
    }
  }
  .overlay{
    background-color: rgba(0, 0, 0, 0.2) 
  }
  // .filter-overlay{
  //   @extend .overlay;
  //   top: 92px;
  // }
  .van-tabbar-item--active{
    .van-tabbar-item__text{
      span{
        color: #1bbc9b;
      }
    }
  }
  .history-search-wp{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f3f3f3;
    height: 45px;
    font-size: 15px;
    color: #1bbc9b;
    .left-arrow-wp,
    .right-arrow-wp{
      display: flex;
      align-items: center;
      position: absolute;
      padding: 0 10px;
      height: 45px;
    }
    .left-arrow-wp{
      left: 15px;
    }
    .right-arrow-wp{
      right: 15px;
    }
    .left-arrow,
    .right-arrow{
      width: 9.5px;
      height: 17px;
    }
    .text{
      padding: 0 30px;
      color: #666;
    }
  }
  // .van-popup{
  //   width: 100%;
  // }
  // .van-picker{
  //   position: fixed;
  //   width: 100%;
  //   bottom: 0;
  // }
  @extend %pagination;
}
</style>



