<template>
  <div id="four">
    <div class="nav-wp">
      <van-nav-bar title="缺陷管理" @click-left="goBack" @click-right="addDefect" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="./zong-fanhui.png" alt="" class="goback-btn">
        </div>
        <div class="add-btn-wp" slot="right">
          <img src="./xinzeng.png" alt="" class="add-btn">
        </div>
      </van-nav-bar>
      <!-- search-input -->
      <div class="search-wp">
        <van-cell-group>
          <van-field v-model.trim="terminalName" @input="searchTerminal"/>
          <div class="placeholder-wp" v-if="showPlaceholder">
            <van-icon name="search" class="search-icon"></van-icon>
            <span class="placeholder">请输入终端名称</span>
          </div>
        </van-cell-group>
      </div>
      <!-- sub navbar -- filter -->
      <van-tabbar v-model="subTabbarActive" class="sub-tabbar">
        <van-tabbar-item v-for="(item, index) in subbarList" :key="index" @click="filterDropdownClick(item, index)">
          <span>{{item.activeItemValue || item.label}}</span>
          <div slot="icon" slot-scope="props" class="sub-tabbar-icon-wp">
            <img src="./xiala02.png" alt="" v-if="props.active">
            <img src="./xiala01.png" alt="" v-else>
          </div>
        </van-tabbar-item>
      </van-tabbar>
    </div>
    <split-view></split-view>
    <no-result v-if="noResult"></no-result>
    <div class="main-wp" v-else>
      <div class="con-wp">
        <div class="main-item" v-for="(item, index) in curPageList" :key="index">
          <div class="title-wp">
            <span class="title">{{errTypeObj[item.defectType]}}</span>
            <span class="submit-time">发现时间：{{item.discoverTime}}</span>
          </div>
          <div class="info-wp">
            <div class="info-list">
              <div class="info-item">
                <span class="title">缺陷标识</span>
                <span class="text">{{item.defectId}}</span>
              </div>
              <div class="info-item">
                <span class="title">设备名称</span>
                <span class="text">{{item.devName}}</span>
              </div>
              <div class="info-item">
                <span class="title">所属线路</span>
                <span class="text">{{item.feederName}}</span>
              </div>
            </div>
            <div class="confirm-btn" @click="goConfirmDefect(item)">缺陷确认</div>
          </div>
        </div>
      </div>
      <van-pagination v-model="curPageIndex" class="pagination" force-ellipses :total-items="totalLen" :items-per-page="itemsPerPage" v-if="totalLen" :show-page-size="2"/>
    </div>
    <!-- 条件筛选 popup -->
    <van-popup v-model="filterPopupShow" class="filter-popup" overlay-class="filter-overlay">
      <span class="item" v-for="(item, index) in filterList" :key="index" @click="filterColumnItemClick(item, index)" :class="{active: subTabItemActive === index}">{{item.label}}</span>
    </van-popup>
    <div class="bottom-tabbar van-hairline--top">
      <div class="tabbar-item active">
        <span>未确认缺陷</span>
      </div>
      <div class="split"></div>
      <div class="tabbar-item" @click="goConfirmedDefect">
        <span>已确认缺陷</span>
      </div>
    </div>
  </div>
</template>

<script>
// 组件
import SplitView from '@/components/split-view/index';
import NoResult from '@/components/no-result/index';
// mixin
import commonApiMixin from '@/mixins/common-api-mixin';
import apiMixin from './mixins/api-mixin';
// util
import _util from '@/util/index';

const subbarList = [
  {label: '区域', type: 0, activeItemIndex: 0, activeItemValue: ''},
  {label: '线路', type: 1, activeItemIndex: 0, activeItemValue: ''},
  {label: '缺陷类型', type: 2, activeItemIndex: 0, activeItemValue: ''},
];

const filterActiveList = [
  {
    index: 0,
    indexValue: '',
  },
  {
    index: 0,
    indexValue: '',
  },
  {
    index: 0,
    indexValue: '',
  },
];

// 显示
const errTypeObj = {
  '5': '终端长期离线',
  '6': '终端频繁投退',
  '7': '开关遥控失败',
  '8': '信号抖动',
  '9': '数据缺漏异常',
  '10': '电源故障',
  '11': '蓄电池故障',
  '12': '流量越限',
  '99': '其他',
};

const allErrsList = [
  {
    label: '全部',
    type: 0,
  },
  {
    label: '终端长期离线',
    type: 5,
  },
  {
    label: '终端频繁投退',
    type: 6,
  },
  {
    label: '开关遥控失败',
    type: 7,
  },
  {
    label: '信号抖动',
    type: 8,
  },
  {
    label: '数据缺漏异常',
    type: 9,
  },
  {
    label: '电源故障',
    type: 10,
  },
  {
    label: '蓄电池故障',
    type: 11,
  },
  {
    label: '流量越限',
    type: 12,
  },
  {
    label: '其他',
    type: 99,
  },
];

export default {
  mixins: [apiMixin, commonApiMixin],
  watch: {
    terminalName: {
      handler(value) {
        if(value) {
          this.showPlaceholder = false;
        } else {
          this.showPlaceholder = true;
        }
      },
      immediate: true
    }
  },
  data() {
    return {
      terminalName: '',
      subTabbarActive: -1,
      subTabItemActive: '',

      subbarList,
      filterActiveList,

      filterPopupShow: false,
      filterList: [],

      areaList: '',
      feederList: '',
      allErrsList,
      errTypeObj,

      areaId: '',
      // 以下三个参数供 查询缺陷用；
      // default：0 代表所有
      feederId: 0,
      // default: 0 代表所有
      defectType: 0,

      ifConfirm: 0,

      tableList: [],

      // 分页
      curPageIndex: 1,
      itemsPerPage: 10,

      showPlaceholder: true,
      allTableList: [],
    }
  },
  computed: {
    // 分页
    totalLen() {
      return this.tableList.length;
    },
    curPageList() {
      return this.tableList.slice(this.itemsPerPage * (this.curPageIndex - 1), this.itemsPerPage * this.curPageIndex);
    },
    noResult() {
      return this.tableList.length ? false : true;
    }
  },
  methods: {
    searchTerminal() {
      if(this.allTableList.length && this.terminalName) {
        this.tableList = this.allTableList.filter((item, index) => {
          if(item.devName.indexOf(this.terminalName) > -1) {
            return true;
          } else {
            return false;
          }
        })
      } else {
        this.tableList = [];
      }
    },
    goBack(){
      // this.$router.replace({path: '/'});
      // android go to index.html;
      webCtrl.finish()
    },
    // 新增缺陷
    addDefect() {
      this.$router.push({name: 'addDefect'});
    },
    goConfirmDefect(item) {
      this.$router.push({name: 'confirmDefect', params: item});
    },
    goConfirmedDefect() {
      this.$router.push({name: 'confirmedDefect'});
    },
    filterDropdownClick(item, index) {
      // 点击区域
      if(item.type === 0) {
        if(this.areaList.length) {
          this.filterList = this.areaList;
        }
        else {
          this.$toast('无区域列表');
          return;
        }
      }
      // 点击线路
      else if(item.type === 1) {
        if(this.feederList.length) {
          this.filterList = this.feederList;
        }
        else {
          this.$toast('无线路列表');
          return;
        }
      }
      // 点击异常类型
      else if(item.type === 2) {
        // 根据当前nav active选取异常类型的list
        this.filterList = this.allErrsList;
      }
      this.subTabItemActive = this.filterActiveList[item.type].index;
      this.filterPopupShow = true;
    },
    filterColumnItemClick(item, index) {
      // 当前点击项属于哪个subbar Index
      // this.subTabbarActive

      let subbarItem = this.subbarList[this.subTabbarActive];
      // 保存点击的item，供其他tabbar切换时使用；
      this.filterActiveList[subbarItem.type].index = index;
      this.filterActiveList[subbarItem.type].indexValue = item;

      // 点击某个区域； 获取对应线路
      if(this.subTabbarActive === 0 ) {
        this.areaId = item.id;
        this.subbarList[0].activeItemValue = item.label;
        this.subbarList[0].activeItemIndex = index;
        this.subbarList[1].activeItemValue = '';
        this.subbarList[1].activeItemIndex = -1;

        this.tableList = [];
        this.filterActiveList[1].index = -1;
      
        this.getFeederByAreaId(this.areaId).then(() => {
          this.feederList.shift();

          if(this.feederList.length) {
            this.subTabbarActive = 1;
            this.filterDropdownClick(this.subbarList[1], 1);
          } else {
            this.$toast('此区域下无变电站')
          }
        })
      }
      // 点击线路
      else if(this.subTabbarActive === 1) {
        this.feederId = item.id;

        this.subbarList[1].activeItemValue = item.label;
        this.subbarList[1].activeItemIndex = index;

        this.getDefectInfo();
      }
      // 点击缺陷类型
      else if(this.subTabbarActive === 2) {
        this.defectType = item.type;

        this.subbarList[2].activeItemValue = item.label;
        this.subbarList[2].activeItemIndex = index;
        
        this.getDefectInfo();
      }
      this.filterPopupShow = false;
    }
  },
  components: {
    SplitView,
    NoResult
  },
  created() {
    // 获取全部缺陷列表
    this.getAllDefectInfo();

    this.getAreaList().then(() => {
      this.areaList.shift();
      if(!this.areaList.length) return;
      this.subbarList[0].activeItemValue = this.areaList[0].label;
      this.getFeederByAreaId(this.areaList[0].id).then(() => {
        this.feederList.shift();
        if(!this.feederList.length) return;
        this.subbarList[1].activeItemValue = this.stationList[0].label
        // 获取缺陷列表
        this.getDefectInfo();
      });
    });
  }
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#four{
  height: 100%;
  .nav-wp{
    height: 139.5px;
  }
  @extend %nav-with-one-title;
  .search-wp{
    position: relative;
    overflow: hidden;
    margin: 15px 12.5px 0 12.5px;
    border-radius: 34px;
    border: 1px solid #dfdfdf;
    height: 34px;
    .placeholder-wp{
      position: absolute;
      pointer-events: none;
      z-index: 5;
      left: 50%;
      top: 50%;
      display: flex;
      justify-content: center;
      flex-direction: row;
      font-size: 14px;
      color: #b5b5b4;
      transform: translate(-50%, -50%);
       .search-icon{
        padding-right: 5px;
      }
    }
   
  }
  @extend %sub-tabbar;
  .sub-tabbar{
    top: 97.5px;
  }
  .main-wp{
    height: calc(100% - 202px);
    overflow-y: auto;
    .con-wp{
      min-height: 100%;
      padding-bottom: 40px;
    }
    .main-item{
      &.warning{
        .title-wp{
          background: #f8b551;
        }
        .confirm-btn{
          background-color: #f8b551;
        }   
      }
      .title-wp{
        display: flex;
        justify-content: space-between;
        padding: 0 15px;
        height: 30px;
        line-height: 30px;
        color: #fff;
        background: #33d8b6;
        .title{
          font-size: 14px;
        }
        .submit-time{
          font-size: 13px;
        }
      }
      .info-wp{
        position: relative;
        margin: 4px 0;
        padding: 0 15px;
        background: #fff;

        .info-list{
          .info-item{
            height: 30px;
            line-height: 30px;
            font-size: 0;
            .title{
              display: inline-block;
              vertical-align: top;
              width: 22%;
              font-size: 14px;
              color: #999;
            }
            .text{
              display: inline-block;
              width: 50%;
              font-size: 14px;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
              color: #333;
            }
          }
        }
        .confirm-btn{
          position: absolute;
          right: 15px;
          top: 50%;
          transform: translateY(-50%);
          padding: 6.5px 7.5px;
          font-size: 12px;
          text-align: center;
          color: #fff;
          background: #33d8b6;
          border-radius: 2px;
        }
      }
    }
    .pagination{
      margin-top: -40px;
    }
  }
  .bottom-tabbar {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 47.5px;
    line-height: 47.5px;
    font-size: 0;
    color: #333;
    background-color: #fff;
    &::after{
      border-top-color: #999;
    }
    .active{
      color: #1abd9b;
    }
    .tabbar-item{
      display: inline-block;
      width: 50%;
      font-size: 15px;
      text-align: center;
    }
    .split{
      position: absolute;
      top: 50%;
      left: 50%;
      height: 20px;
      width: 0px;
      transform: translate(-50%, -50%);
      border-left: 1px solid #999;
    }
  }
  .van-cell{
    padding: 0;
    height: 34px;
    line-height: 34px;
    .van-hairline--top-bottom::after{
      content: none;
      border: 0;
      height: 0;
    }
    .van-cell__value::after{
      height: 0;
      content: none;
      border: 0;
    }
    .van-field__control {
      font-size: 14px;
      text-align: center;
      &:focus {
        color: #666;
      }
    }
    input:-ms-input-placeholder{
      color: #c7c7c7;
    }
    input::-webkit-input-placeholder{
      color: #c7c7c7;
    }
  }
  .van-cell-group::after{
    content: none;
  }
  .filter-popup {
    top: 139.5px;
    width: 101%;
    max-height: 50%;
    overflow-y: auto;
    background-color: #f6f6f6;
    .item{
      display: block;
      padding: 0 9.5px 0 40px;
      line-height: 45px;
      font-size: 14px;
      text-align: left;
      &.active{
        color: #1bbc9b;
      }
    }
    left: 0;
    transform: translate3d(0,0,0);
    -webkit-transform: translate3d(0,0,0);
  }
  .filter-overlay{
    background-color: rgba(0, 0, 0, 0.2);
    top: 139.5px;
  }
  @extend %pagination;
}
</style>


