import * as types from '../mutation-types';

export default {
  state: {
    confirmName: '',
    feederId: '',
    areaId: '',
    device: {
      label: '',
      id: ''
    },
    defectLevel: {
      label: '',
      id: '',
    },
    defectType: {
      label: '',
      id: '',
    },
    lastHandleTime: '',
    defectDesc: '',
  },
  mutations: {
    [types.SET_ADD_DEFECT_CONFIRM_NAME](state, value) {
      state.confirmName = value;
    },
    [types.SET_ADD_DEFECT_FEEDER_ID](state, value) {
      state.feederId = value;
    },
    // [types.SET_ADD_DEFECT_AREA_ID](state, value) {
    //   state.areaId = value;
    // },
    [types.SET_ADD_DEFECT_DEVICE](state, obj) {
      state.device = obj;
    },
    [types.SET_ADD_DEFECT_LEVEL](state, obj) {
      state.defectLevel = obj;
    },
    [types.SET_ADD_DEFECT_TYPE](state, obj) {
      state.defectType = obj;
    },
    [types.SET_ADD_DEFECT_LAST_HANDLE_TIME](state, value) {
      state.lastHandleTime = value;
    },
    [types.SET_ADD_DEFECT_DESC](state, value) {
      state.defectDesc = value;
    },
  },
  actions: {
    saveAddDefectInfo({commit}, {confirmName, device, defectLevel, defectType, lastHandleTime, defectDesc}) {
      let originObj = {
        label: '',
        id: '',
      }
      commit(types.SET_ADD_DEFECT_CONFIRM_NAME, confirmName || '');
      commit(types.SET_ADD_DEFECT_DEVICE, device || originObj);
      commit(types.SET_ADD_DEFECT_LEVEL, defectLevel || originObj);
      commit(types.SET_ADD_DEFECT_TYPE, defectType || originObj);
      commit(types.SET_ADD_DEFECT_LAST_HANDLE_TIME, lastHandleTime || '');
      commit(types.SET_ADD_DEFECT_DESC, defectDesc || '');
    },
  }
};



