import * as types from '../mutation-types';

export default {
  state: {
    pushTimeType: {},
    deviceType: {},
    // 已选取的设备列表
    equipSelList: [],
    // 设备异常的列表；
    tableList: [],

    warnTypeLabel: '',
    warnType: '',
    warnStatus: [],
  },
  mutations: {
    [types.SET_PUSHTIME_TYPE](state, obj) {
      state.pushTimeType = obj;
    },
    [types.SET_EQUIP_SEL_LIST](state, value) {
      state.equipSelList = value;
    },
    [types.SET_TABLE_LIST](state, arr) {
      state.tableList = arr;
    },

    [types.SET_WARN_TYPE](state, value) {
      state.warnType = value;
    },
    [types.SET_WARN_STATUS](state, value) {
      state.warnStatus = value;
    },
    [types.SET_WARN_TYPE_LABEL](state, value) {
      state.warnTypeLabel = value;
    },
    [types.SET_DEVICE_TYPE](state, value) {
      state.deviceType = value;
    },
  },
  actions: {
    changeSubs({commit}, {pushTimeType, equipSelList}) {
      commit(types.SET_PUSHTIME_TYPE, pushTimeType);
      commit(types.SET_EQUIP_SEL_LIST, equipSelList);
    },
    changeWarnType({commit}, {warnType, warnStatus, warnTypeLabel}) {
      commit(types.SET_WARN_TYPE, warnType);
      commit(types.SET_WARN_STATUS, warnStatus);
      commit(types.SET_WARN_TYPE_LABEL, warnTypeLabel);
    }
  }
};



